module.exports = "1\n\n00:03  \n\nHello {{name}},\n\n2\n\n00:06 \n\nIf you’ve made it here, then congratulations!\n\n3\n\n00:09 \n\nBecause this means that your archetype is the Caregiver!\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/caregiver.png', fadeIn: true} {{/fn}}\n\n4\n\n00:12 \n\nBut what does this actually mean?\n\n5\n\n00:15 \n\nDon’t worry, everything you need to know will be on this page.\n\n6\n\n00:19 \n\nStick to the end of it and I promise you that you’ll experience something truly magical.\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/caregiver.png', fadeOut: true} {{/fn}}\n\n7\n\n00:25 \n\nThe quiz that you’ve just taken is designed to...\n\n8\n\n00:29 \n\n...Identify your archetype – a personality profile that’s...\n\n9\n\n00:32 \n\n...broken down your entire being.\n\n10\n\n00:35 \n\nYour tendencies, your strengths, your past, and your future.\n\n11\n\n00:40 \n\nYour archetype takes into account your gender...\n\n12\n\n00:43 \n\nYour marital status...\n\n13\n\n 00:45 \n\nHow you answered the quiz...\n\n14\n\n 00:46 \n\nAnd how long you took to get those answers.\n\n15\n\n00:49 \n\nThat’s right, {{name}}.\n\n16\n\n00:50 \n\nYou can use that information to make astronomical changes in your life.\n\n17\n\n00:54 \n\nYou’ll be able to leverage on your strengths...\n\n18\n\n00:57 \n\nImprove on your weaknesses...\n\n19\n\n00:59 \n\nAnd embark on the life that you’ve always dreamed of.\n\n20\n\n01:02 \n\nBut before we get into that...\n\n21\n\n01:04 \n\nI want to share with you about your archetype...\n\n22\n\n01:07 \n\nThe Caregiver, and what it means.\n\n23\n\n01:10 \n\n{{name}}, you truly are a unique individual.\n\n24\n\n01:13 \n\nAnd I genuinely believe that this world needs more people like you.\n\n25\n\n01:18 \n\nYou are a mature and deeply intelligent being...\n\n26\n\n01:21 \n\nnot necessarily in the sense of academics...\n\n27\n\n01:24 \n\n...But in terms of thinking differently.\n\n28\n\n01:27 \n\nWhile the world walks in one direction...\n\n29\n\n01:29 \n\n...You have no qualms with going against it.\n\n30\n\n01:32 \n\nThat’s what makes you so special, {{name}}.\n\n31\n\n01:36 \n\nYou’re also deeply sensitive.\n\n32\n\n01:37 \n\nYou’re able to see and feel all the pains of the world.\n\n33\n\n01:41 \n\nThat’s because as a Caregiver, you are highly compassionate.\n\n33\n\n01:45 \n\nYour selflessness comes unparalleled.\n\n34\n\n01:48 \n\nYou’re always willing to extend a helping hand to...\n\n35\n\n01:51  \n\n...Your friends, strangers...\n\n36\n\n01:53 \n\n...And sometimes, even to your enemies.\n\n37\n\n01:56 \n\n{{name}}, you might not know it yet...\n\n38\n\n01:57 \n\nBut your generosity inspires everyone around you...\n\n39\n\n02:01 \n\n...The people that you help, and the people who witness you helping others.\n\n40\n\n02:05 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeIn: true} {{/fn}}\n\nThere is something else about you, {{name}}…\n\n41\n\n02:08 \n\nYou are the one who holds the world together.\n\n42\n\n02:12 \n\nI’m getting the sense that you’re going to live...\n\n43\n\n02:13 \n\n...A long, healthy, and fulfilled life.\n\n44\n\n02:16 \n\nAnd although success might not be at your feet right now...\n\n45\n\n02:20 \n\n...Your riches and fulfilment are merely waiting for you.\n\n46\n\n02:24 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeOut: true} {{/fn}}\n\n{{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}\n\nBut there’s something else, {{name}}.\n\n47\n\n02:27 \n\nEven though you will triumph...\n\n48\n\n02:28 \n\n...Your path to success will not come easy.\n\n49\n\n02:31 \n\nYour sympathetic and giving nature...\n\n50\n\n02:33 \n\n...Makes you vulnerable to persecutions.\n\n51\n\n02:36 \n\nIn fact...\n\n52\n\n02:37 \n\nYou might have already experienced such instances already.\n\n53\n\n02:41 \n\nBut {{name}}...\n\n54\n\n02:42 \n\nDon’t let that discourage you.\n\n55\n\n02:44 \n\nThe world needs your altruistic and giving nature.\n\n56\n\n02:48 \n\nThe world needs people like you to be in positions of power.\n\n57\n\n02:52 \n\nYou are the one who keeps the world grounded.\n\n58\n\n02:55 \n\nYou are the one who keeps the hungry fed.\n\n59\n\n02:58 \n\nYou are the one who keeps the children safe.\n\n60\n\n03:01 \n\nAnd you will find yourself in that position of power soon…\n\n61\n\n03:05 \n\n...Very soon.\n\n62\n\n03:06 \n\nYou will encounter wicked hearts and selfish people...\n\n63\n\n03:10 \n\nBut don’t let that lose sight of who you truly are, {{name}}.\n\n64\n\n03:13 \n\n{{name}}, I can already tell...\n\n65\n\n03:15 \n\n...That your friends and family really look up to you.\n\n66\n\n03:18 \n\nYou’re the one who they come to for detailed advice...\n\n67\n\n03:21 \n\nA listening ear...\n\n68\n\n03:22 \n\nOr just for a quick chat.\n\n69\n\n03:25 \n\nYou’re not the type of person to have a huge circle of friends.\n\n70\n\n03:28 \n\nBut you’re not someone who has trouble with making friends.\n\n71\n\n03:31 \n\nIn fact...\n\n72\n\n03:32 \n\nYou have no trouble at all…\n\n73\n\n03:34 \n\n...When it comes to impressing others with your intelligence and sweet nature.\n\n74\n\n03:38 \n\nBut you’re extremely selective with who you choose to let into your life.\n\n75\n\n03:42 \n\nYou prefer to keep your loved ones close, and your wolf pack small.\n\n76\n\n03:47 \n\n{{name}}, you are incredibly nurturing.\n\n77\n\n03:50 \n\nPeople see you as a mother-like figure.\n\n78\n\n03:52 \n\nThis makes you a natural-born teacher…\n\n79\n\n03:55 \n\n...But not really in the sense of education.\n\n80\n\n03:57 \n\nI sense that you will find success and fulfilment as…\n\n81\n\n04:01 \n\nA counsellor, a nurse, a trainer, or a guide of some sort.\n\n82\n\n04:06 \n\nYou will also excel in non-profit organizations and charities.\n\n83\n\n04:11 \n\nYou can be very protective, {{name}}.\n\n84\n\n04:13 \n\nEspecially as a parent.\n\n85\n\n04:16 \n\nYou love your children, friends, and family fiercely...\n\n86\n\n04:20 \n\n...And you are willing to give and sacrifice everything that you have for them.\n\n87\n\n04:25 \n\n{{name}}, you are going to make a lasting mark in your lifetime.\n\n88\n\n04:29 \n\nYou’re able to look at the world from a broad and altruistic perspective.\n\n89\n\n04:34 \n\nYou see the big picture...\n\n90\n\n04:36 \n\n...And you will do everything that you can to make it a better place.\n\n91\n\n04:40 \n\nEach archetype has a calling in life.\n\n92\n\n04:43 \n\nA calling that will lead you to joy and fulfilment.\n\n93\n\n04:46 \n\n{{name}}, I’d like to share with you what I’ve discovered about your calling.\n\n93\n\n04:51 \n\n{{name}}, you tend to concern yourself with acts of sharing.\n\n94\n\n04:55 \n\nYou’re always happy to share your knowledge...\n\n95\n\n04:57 \n\n...Wisdom, and experiences with those around you.\n\n96\n\n05:01 \n\nThat is where you derive fulfilment from.\n\n97\n\n05:04 \n\nThe more you share, the more you express care for others...\n\n98\n\n05:07 \n\n...The happier you seem to be.\n\n99\n\n05:10 \n\nHowever, {{name}}...\n\n100\n\n05:11 \n\n...That can make you dependent on others for your own happiness.\n\n101\n\n05:14 \n\nAnd that is something that you need to be careful of.\n\n102\n\n05:17 \n\nAt times, it might feel as if you’re living your life for others.\n\n103\n\n05:21 \n\nThat’s not what you want, {{name}}.\n\n104\n\n05:23 \n\nYou want to live life for yourself.\n\n105\n\n05:26 \n\nYou want to love yourself.\n\n106\n\n05:28 \n\nWhen you’re able to do that...\n\n107\n\n05:30 \n\n...Success and the life of your dreams will follow.\n\n108\n\n05:33 \n\nAnd that’s what Individuation aims to teach you.\n\n109\n\n05:36 \n\nBut there’s more to what I’ve learned about you, {{name}}…\n\n110\n\n05:40 \n\nIt feels to me that you’re a deeply emotional and spiritual person.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n\n111\n\n05:45 \n\nYou’re connected to your soul in a way that’s far more profound than others.\n\n112\n\n05:50 \n\nYour thought process does not follow the rules and logic of the world.\n\n113\n\n05:54 \n\nIn fact, I get the sense that you have quite the imagination.\n\n114\n\n05:58 \n\nYour thoughts and dreams are vivid, colourful, and symbolic of who you are as an individual.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n\n115\n\n06:05 \n\n{{name}}, you’re not someone who likes to argue.\n\n116\n\n06:08 \n\nYou tend to avoid conflict…\n\n117\n\n06:10 \n\n...And you’re uncanny when it comes to devising win-win situations.\n\n118\n\n06:14 \n\nBut when backed into a corner, you’re not one to back down.\n\n119\n\n06:18 \n\nYou fight fiercely for your loved ones and for righteousness.\n\n120\n\n06:23 \n\nYou’re the first person to step in when you see a fight breaking out.\n\n121\n\n06:27 \n\nYou’re the first person to reach for your pockets in the sight of a struggling busker.\n\n122\n\n06:31 \n\nYou’re the first person your friends call when they’ve had a horrible day.\n\n123\n\n06:36 \n\nYou’re the one who people rely on and look up to.\n\n124\n\n06:40 \n\n{{name}}, you are the role model of the world.\n\n125\n\n06:44 \n\nBut every superhero needs a side-kick…\n\n126\n\n06:46 \n\nA partner in crime who will stick by them when the going gets tough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n\n127\n\n06:50 \n\nThis little chart I’ve prepared represents...\n\n128\n\n06:53 \n\n...the archetypes that you’re most likely to end up with.\n\n129\n\n06:56 \n\nOr if you’re already in a committed relationship...\n\n130\n\n06:59 \n\n...There’s a really good chance that your spouse is one of these archetypes.\n\n131\n\n07:03 \n\n{{name}}, you’re not the kind of person that has trouble when it comes to love.\n\n132\n\n07:08 \n\nLove interests find themselves drawn to your nurturing and giving nature.\n\n133\n\n07:13 \n\nYou’re sort of a rare breed when it comes to love.\n\n134\n\n07:16 \n\nYou’re fiercely loyal, patient, giving, and emotionally connected.\n\n135\n\n07:21 \n\nThe relationships that you forge with potential lovers...\n\n136\n\n07:24 \n\n...Are always profound and deep.\n\n137\n\n07:27 \n\nYou become very selective with who you choose to date...\n\n138\n\n 07:30 \n\n...And who you choose to end up with…\n\n139\n\n07:32 \n\nFor good reason, of course.\n\n140\n\n07:35 \n\nI sense that in the coming years...\n\n141\n\n07:37 \n\nYou will be the happiest you’ve ever been in that regard.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n\n142\n\n07:40 \n\nBut don’t forget, {{name}}...\n\n143\n\n07:42 \n\nHow you perceive yourself should not be determined by your love life.\n\n144\n\n07:47 \n\nYour spouse or partner is meant to play a supporting role in your life...\n\n145\n\n07:51 \n\n...Not the lead.\n\n146\n\n07:53   \n\nFocus on your own personal growth...\n\n147\n\n07:55 \n\n...and I promise you, {{name}}...\n\n148\n\n07:58 \n\n...You will soon discover heavenly rewards."

