module.exports = "00:03 \n\n1\n\nHello {{name}},\n\n00:05 \n\n2\n\nIf you’ve made it here, then congratulations!\n\n00:09 \n\n3\n\nBecause this means that your archetype is the Sage!\n\n00:12 \n\n4\n\nBut what does this actually mean?\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/sage.png', fadeIn: true} {{/fn}}\n\n00:15 \n\n5\n\nDon’t worry, everything you need to know will be on this page.\n\n00:19 \n\n6\n\nStick to the end of it and I promise that\n\n00:22 \n\n7\n\nyou’ll experience something truly magical.\n\n00:25 \n\n8\n\nThe quiz that you’ve just taken is designed to identify your archetype\n\n00:30 \n\n9\n\na personality profile that’s broken down your entire being.\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/sage.png', fadeOut: true} {{/fn}}\n\n00:35 \n\n10\n\nYour tendencies, your strengths, your past, and your future.\n\n00:40 \n\n11\n\nYour archetype takes into account your gender,\n\n00:43 \n\n12\n\nyour marital status, how you answered the quiz,\n\n00:47 \n\n13\n\nand how long you took to get those answers.\n\n00:50 \n\n14\n\nThat’s right, {{name}}.\n\n00:52 \n\n15\n\nYou can use that information to make astronomical changes in your life.\n\n00:56 \n\n16\n\nYou’ll be able to leverage on your strengths, improve on your weaknesses,\n\n01:01 \n\n17\n\nand embark on the life that you’ve always dreamed of.\n\n01:04 \n\n18\n\nBut before we get into that, I want to share with you about your archetype, The Sage,\n\n01:10 \n\n19\n\nand what it means.\n\n01:12 \n\n20\n\n{{name}}, you truly are a unique individual.\n\n01:15 \n\n21\n\nAnd I genuinely believe that this world needs more people like you.\n\n01:19 \n\n22\n\nYou are a wise soul that’s overflowing with knowledge and wisdom.\n\n01:24 \n\n23\n\nIf you were a vehicle, knowledge and wisdom would be your fuel – it’s what drives you.\n\n01:31 \n\n24\n\nYou’re different that way, {{name}}.\n\n01:33 \n\n25\n\nWhile the world walks in one direction, you have no qualms with going against it.\n\n01:39 \n\n26\n\nThat’s what makes you so special, {{name}}.\n\n01:43 \n\n27\n\nYou’re deeply intuitive and introspective.\n\n01:47 \n\n28\n\nUnlike others, your education doesn’t stop after graduating from college or university.\n\n01:53 \n\n29\n\nYou are constantly applying and enriching yourself throughout the course of your life.\n\n01:57 \n\n30\n\n{{name}}, you might not know it yet,\n\n01:59 \n\n31\n\nbut you are more morally and ethically upright than others.\n\n02:03 \n\n32\n\nYou hold your values and principles close to your heart,\n\n02:06 \n\n33\n\nand you’re always ready to stick to your guns.\n\n02:09 \n\n34\n\n{{#fn}} {fn: 'sliderImageGlobe', fadeIn: true} {{/fn}}\n\nThere is something else about you, {{name}}…\n\n02:11 \n\n35\n\nYou form the foundation of the world’s progress and advancement.\n\n02:17 \n\n36\n\n{{name}}, I’m getting the sense that you’re going to live a long, and happy life.\n\n02:21 \n\n37\n\nAnd although success might not be at your feet right now,\n\n02:24 \n\n38\n\nyour riches and fulfilment are merely waiting for you.\n\n02:27 \n\n39\n\n{{#fn}} {fn: 'sliderImageGlobe', fadeOut: true} {{/fn}}\n\n{{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}\n\nBut there’s something else, {{name}}.\n\n02:30 \n\n40\n\nEven though you will triumph, your path to success will not come easy.\n\n02:35 \n\n41\n\nYou must ultimately defeat and control your hatred for ignorance.\n\n02:39 \n\n42\n\nIt is vital for you to understand that not everyone is capable of learning at your pace,\n\n02:44 \n\n43\n\nand not everyone shares the same passion as you do for knowledge.\n\n02:48 \n\n44\n\n{{name}}, don’t let that discourage you.\n\n02:50 \n\n45\n\nThe world needs your wisdom.\n\n02:52 \n\n46\n\nThe world needs people like you to be in positions of power.\n\n02:56 \n\n47\n\nYou keep the world moving forward.\n\n02:59 \n\n48\n\nYou do, however, need to understand that it’s important for you\n\n03:03 \n\n49\n\nto exercise humility in both your thoughts and actions.\n\n03:06 \n\n50\n\nThat way, you’ll be able to build trust and create even more fruitful interactions.\n\n03:11 \n\n51\n\nAfter all, {{name}}, you tend to struggle when it comes to taking action\n\n03:15 \n\n52\n\nand humility will lead you down the road of embracing failure,\n\n03:19 \n\n53\n\nwhich will then lead you to the path towards success.\n\n03:21 \n\n54\n\n{{name}}, I can already tell that your friends and family really look up to you.\n\n03:26 \n\n55\n\nYou’re the one who they come to for detailed advice, a listening ear,\n\n03:30 \n\n56\n\nor just for a quick chat.\n\n03:32 \n\n57\n\nYou’re not the type of person to have huge circles of friends.\n\n03:36 \n\n58\n\nYou’re not someone who has trouble with making friends.\n\n03:39 \n\n59\n\nIn fact, you have no trouble at all when it comes to impressing others\n\n03:43 \n\n60\n\nwith your intelligence and wisdom.\n\n03:45 \n\n61\n\nBut you’re extremely selective with who you choose to let into your life.\n\n03:49 \n\n62\n\nYou prefer to keep your loved ones close, and your wolf pack small.\n\n03:53 \n\n63\n\n{{name}}, you are incredibly nurturing.\n\n03:57 \n\n64\n\nYou possess the unique ability to connect with others\n\n04:00 \n\n65\n\nand blend into any social group.\n\n04:03 \n\n66\n\nYou have a great deal to offer to this world. {{name}},\n\n04:05 \n\n67\n\nno one is able to play the role of an advisor and guide as excellently as you.\n\n04:10 \n\n68\n\nI sense that you will find success and fulfilment\n\n04:14 \n\n69\n\nas a business advisor, a researcher, a data scientist, or a consultant.\n\n04:19 \n\n70\n\nYou can be very intuitive, {{name}}.\n\n04:22 \n\n71\n\nDespite your great capacity for knowledge, you prioritize your intuition over facts,\n\n04:28 \n\n72\n\nespecially when it comes to making important decisions.\n\n04:31 \n\n73\n\nAnd that’s what works best for you.\n\n04:33 \n\n74\n\n{{name}}, you are going to make a lasting mark in your lifetime.\n\n04:38 \n\n75\n\nYou’re able to look at the world from a broad and altruistic perspective.\n\n04:42 \n\n76\n\nYou see the big picture, and you have what it takes to\n\n04:45 \n\n77\n\nmake this world a richer and more informed place for everyone.\n\n04:48 \n\n78\n\n{{name}}, you are practically a bookworm\n\n04:51 \n\n79\n\n– possessing the uncanny ability to bury yourself with endless readings.\n\n04:56 \n\n80\n\nYou take an interest in a broad number of subjects, and your fulfilment is derived from learning.\n\n05:01 \n\n81\n\nYou excel at one-on-one interactions,\n\n05:04 \n\n82\n\nand never fail to make an excellent impression on others during such situations.\n\n05:08 \n\n83\n\nHowever, it is vital for you to understand that your introverted nature\n\n05:12 \n\n84\n\nmakes you struggle to speak to larger groups of people.\n\n05:15 \n\n85\n\nUltimately, {{name}}, you are a realist. You have a clear conscience.\n\n05:21 \n\n86\n\nDiscerning right from wrong has never been a difficult task for you.\n\n05:25 \n\n87\n\nI’m getting the sense that you’re not too concerned about monetary riches,\n\n05:29 \n\n88\n\nalthough you do wish you had more wealth.\n\n05:32 \n\n89\n\nThere is, however, a greater purpose for you in this lifetime, {{name}}.\n\n05:35 \n\n90\n\nHowever, {{name}}, your purpose and direction in life might feel empty at times.\n\n05:39 \n\n91\n\nAnd that is something that you need to be careful of.\n\n05:42 \n\n92\n\nYou might feel as if you’re living your life for the sake of learning,\n\n05:46 \n\n93\n\nand you continue doing what you do because it’s the only thing you know how to do.\n\n05:50 \n\n94\n\nThat’s not what you want, {{name}}. You want to live life for yourself. You want to love yourself.\n\n05:58 \n\n95\n\nWhen you’re able to do that, success and the life of your dreams will follow.\n\n06:03 \n\n96\n\nAnd that’s what Individuation aims to teach you.\n\n06:06 \n\n97\n\nBut there’s more to what I’ve learned about you, {{name}}…\n\n06:10 \n\n98\n\nIt feels to me that you’re a deeply emotional and spiritual person.\n\n06:14 \n\n99\n\nYou’re connected to your soul in a way that’s far more profound than others.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n\n06:19 \n\n100\n\nYour thought process does not follow the rules and logic of the world.\n\n06:23 \n\n101\n\nIn fact, I get the sense that you have quite the imagination.\n\n06:27 \n\n102\n\nYour thoughts and dreams are vivid, colourful, and symbolic of who you are as an individual.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n\n06:33 \n\n103\n\n{{name}}, you’re not someone who likes to argue.\n\n06:37 \n\n104\n\nYou tend to avoid conflict and you’re uncanny\n\n06:40 \n\n105\n\nwhen it comes to devising win-win situations.\n\n06:43 \n\n106\n\nBut when backed into a corner, you’re not one to back down.\n\n06:47 \n\n107\n\nYou fight fiercely for your loved ones and for your righteousness.\n\n06:51 \n\n108\n\nYou’re the first person to step in when you see a fight breaking out.\n\n06:55 \n\n109\n\nYou’re the first person to reach for your pockets in the sight of a struggling busker.\n\n07:00 \n\n110\n\nYou’re the first person your friends call when they’ve had a horrible day.\n\n07:04 \n\n111\n\nYou’re the one who people rely on and look up to.\n\n07:07 \n\n112\n\n{{name}}, you are the guardian of the world.\n\n07:10 \n\n113\n\nBut every superhero needs a side-kick; a partner in crime who will stick by them when the going gets tough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n\n07:17 \n\n114\n\nThis little chart I’ve prepared represents the archetypes\n\n07:21 \n\n115\n\nthat you’re most likely to end up with.\n\n07:23 \n\n116\n\nOr if you’re already in a committed relationship,\n\n07:25 \n\n117\n\nthere’s a really good chance that your spouse is one of those archetypes.\n\n07:29 \n\n118\n\n{{name}}, you’re not the kind of person that has trouble when it comes to love.\n\n07:34 \n\n119\n\nPeople find themselves drawn to your charisma and kind nature.\n\n07:38 \n\n120\n\nYou’re sort of a rare breed when it comes to love.\n\n07:41 \n\n121\n\nYou communicate better than anyone else,\n\n07:43 \n\n122\n\nand you’re an incredibly thoughtful individual.\n\n07:46 \n\n123\n\nThe relationships that you forge with potential lovers are always profound and deep.\n\n07:50 \n\n124\n\nYou become very selective with who you choose to date\n\n07:54 \n\n125\n\nand who you choose to end up with – for good reason, of course.\n\n07:58 \n\n126\n\nI sense that in the coming years, you will be the happiest you’ve ever been in that regard.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n\n08:03 \n\n127\n\n{{name}}, I’m sensing that your love life isn’t going to be the smoothest\n\n08:07 \n\n128\n\n– but you will find that happiness that you’re looking for soon enough.\n\n08:11 \n\n129\n\nBut don’t forget, {{name}}, how you perceive yourself should not be determined by your love life.\n\n08:18 \n\n130\n\nYour spouse or partner is meant to play a supporting role in your life; not the lead.\n\n08:23 \n\n131\n\nFocus on your own personal growth, and I promise you,\n\n08:27 \n\n132\n\n{{name}}, you will soon discover heavenly rewards."

