module.exports = "4.000: Hello {{name}},\n4.000: If you’ve made it here, then congratulations!\n3.000: Because this means that your archetype is the Explorer!{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/explorer.png', fadeIn: true} {{/fn}}\n5.000: But what does this actually mean?\n2.000: Don’t worry, everything you need to know will be on this page.\n5.000: Stick to the end of it and I promise that you’ll experience something truly magical.{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/explorer.png', fadeOut: true} {{/fn}}\n5.000: The quiz that you’ve just taken is designed to identify your archetype...\n6.000: A personality profile that’s broken down your entire being.\n4.000: Your tendencies, your strengths, your past, and your future.\n5.000: Your archetype takes into account your gender, your marital status...\n4.000: How you answered the quiz, and how long you took to get those answers.\n5.000: That’s right, {{name}}.\n2.000: You can use that information to make astronomical changes in your life.\n5.000: You’ll be able to leverage on your strengths...\n3.000: Improve on your weaknesses...\n2.000: And embark on the life that you’ve always dreamed of.\n3.000: But before we get into that...\n2.000: I want to share with you about your archetype, The Explorer, and what it means.\n6.000: {{name}}, you truly are a unique individual.\n4.000: And I genuinely believe that this world needs more people like you.\n4.000: You are a mature and a truly unconventional being.\n3.000: I think the best way to describe you is “an adventurer”.\n4.000: Yes, you are an adventurer looking to make your mark on the world.\n5.000: To discover new lands, new opportunities, and new experiences.\n5.000: You’re different that way, {{name}}.\n2.000: While the world walks in one direction…\n3.000: You have no qualms with going against it.\n3.000: That’s what makes you so special, {{name}}.\n3.000: Your biggest value is freedom.\n3.000: Sitting still for even just a second can make you feel jittery…\n5.000: ...Uneasy, restless, and just completely uncomfortable.\n4.000: That’s because as an Explorer, you are highly energetic.\n5.000: While others struggle to feel comfortable in unfamiliar places...\n5.000: You just seem to feel at home in the most distant of lands.\n3.000: That’s because you envision the world as a singular place…\n5.000: And you understand that each and everyone of us belong to the same home.\n5.000: {{name}}, you might not know it yet...\n2.000: But you are more morally and ethically upright than others.\n4.000: You hold your values and principles close to your heart...\n4.000: And you’re always ready to stick to your guns.\n3.000: There is something else about you, {{name}}…{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeIn: true} {{/fn}}\n3.000: You form the foundation of the world.{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeOut: true} {{/fn}}\n2.000: You play the leading role in uncovering new discoveries.\n5.000: {{name}}, I’m getting the sense that you’re going to live a long, and fruitful life.\n5.000: And although success might not be at your feet right now...\n3.000: Your riches and fulfilment are merely waiting for you.\n4.000: {{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}.\n2.000: Even though you will triumph, your path to success will not come easy.\n6.000: Talking to people is never a problem for you...\n2.000: But I’m getting the sense that you tend to struggle when it comes to...\n4.000: Maintaining healthy relationships.\n2.000: Your nature revolves so much around meeting new people and...\n4.000: Learning about new cultures that you tend to…\n2.000: Forget about the people closest to you.\n3.000: {{name}}, don’t let that discourage you.\n3.000: The world needs your ambition and unquenchable curiosity.\n4.000: The world needs people like you to be in positions of power.\n5.000: You are the one who keeps the world moving forward and making progress.\n4.000: You do, however, need to be less critical about yourself.\n5.000: You’re constantly pushing yourself to climb new heights and explore the world...\n5.000: But you must understand that growth takes time.\n3.000: That way, you’ll be able to be more patient with both yourself, and others.\n6.000: After all, {{name}}, growth is a product of both effort and time...\n5.000: And exercising patience will lead you towards the fulfilment that you seek.\n6.000: {{name}}, I can already tell that your friends and family really look up to you.\n5.000: You’re the one who they come to when they’re in need...\n3.000: When they need someone to talk to...\n2.000: Or even just for a quick chat.\n3.000: You’re not the type of person to have huge circles of friends...\n3.000: Although you’re not someone who has trouble with making friends either.\n4.000: In fact, you have no trouble at all when it comes to impressing others with...\n5.000: Your worldly knowledge and accepting heart.\n2.000: But you’re extremely selective with who you choose to let into your life.\n5.000: You prefer to keep your loved ones close, and your wolf pack small.\n4.000: {{name}}, you are incredibly curious...\n3.000: Which keeps conversations centered around others rather than yourself.\n4.000: You possess the unique ability to connect with others and blend into any social group.\n6.000: You have a great deal to offer to this world.\n3.000: {{name}}, you are a natural-born learner, which makes you an excellent teacher.\n6.000: I sense that you will find success and fulfilment as...\n4.000: An instructor, a philanthropist, a CEO, or a public relations manager.\n5.000: Even though you appear to be laid back and easy going…\n4.000: You can also be very protective, {{name}}.\n3.000: Especially as a parent.\n2.000: You love your children, friends, and family fiercely.\n4.000: You are willing to give and sacrifice everything that you have for them.\n4.000: {{name}}, you are going to make a lasting mark in your lifetime.\n5.000: You’re able to look at the world from a broad and altruistic perspective.\n5.000: You see the big picture, and you have what it takes to make this world more connected.\n6.000: Your deep understanding of human nature and culture as a whole...\n4.000: Gives you extraordinary insight into how you can bring the world closer together.\n6.000: {{name}}, you are practically a chameleon...\n3.000: Possessing the uncanny ability to make yourself at home....\n4.000: Within any social group or social setting.\n3.000: You excel at one-on-one interactions...\n3.000: And never fail to make an excellent impression on larger groups of people.\n5.000: Your charisma and eloquence intrigues those who are fortunate enough...\n5.000: ...To have crossed paths with you.\n2.000: Ultimately, {{name}}, you are a dreamer.\n3.000: You have a strong sense of ambition...\n3.000: And you’re willing to do whatever it takes to achieve your dreams.\n3.000: I’m getting the sense that you’re not too concerned about monetary riches...\n5.000: Although you do wish you had more wealth.\n2.000: There is, however, a greater purpose for you in this lifetime, {{name}}.\n6.000: {{name}}, I understand that you tend to struggle with commitments.\n5.000: And that is something that you need to be careful of.\n3.000: At times, it might feel as if you’re being trapped...\n4.000: And having your freedom taken away. But that’s quite the contrary.\n4.000: Fulfilment ultimately comes from forging valuable relationships...\n5.000: With others, and yourself. And that requires commitment.\n4.000: That’s what you seek, {{name}}.\n3.000: You want to live life for yourself, without appearing selfish.\n4.000: You want to love yourself, while keeping the relationships you’ve forged close to your heart.\n6.000: When you’re able to do that, success and the life of your dreams will follow.\n5.000: And that’s what Individuation aims to teach you.\n4.000: But there’s more to what I’ve learned about you, {{name}}…\n3.000: It feels to me that you’re a deeply emotional and spiritual person.{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n5.000: You’re connected to your soul in a way that’s far more profound than others.\n5.000: Your thought process does not follow the rules and logic of the world.\n4.000: In fact, I get the sense that you have quite the imagination.\n4.000: Your thoughts and dreams are vivid, colourful...\n3.000: And symbolic of who you are as an individual.{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n3.000: {{name}}, you’re not someone who likes to argue.\n4.000: You tend to avoid conflict and you’re uncanny when it comes to devising win-win situations.\n6.000: But when backed into a corner, you’re not one to back down.\n4.000: You fight fiercely for your loved ones and for righteousness.\n4.000: You’re the first person to volunteer in the sight of disaster.\n4.000: You’re the first person to donate your last dime to someone who needs it more.\n5.000: You’re the first person to impact and change the lives of those who are less fortunate.\n6.000: You’re the one who people rely on and look up to.\n4.000: {{name}}, you are the catalyst of change in humanity.\n4.000: But every explorer needs someone to accompany them on their adventures...\n5.000: A partner-in-crime who will stick by them when the going gets tough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n4.000: This little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n7.000: Or if you’re already in a committed relationship...\n2.000: There’s a really good chance that your spouse is one of those archetypes.\n5.000: {{name}}, you’re not the kind of person that has trouble when it comes to love.\n5.000: Love interests find themselves drawn to your charisma and kind nature.\n6.000: You’re sort of a rare breed when it comes to love.\n3.000: You communicate better than everyone else, and you’re a thoughtful individual.\n5.000: The relationships that you forge with potential lovers are always profound and deep.\n5.000: You become very selective with who you choose to date...\n4.000: And who you choose to end up with.\n2.000: For good reason, of course.\n2.000: I sense that in the coming years...\n2.000: You will be the happiest you’ve ever been in that regard.\n4.000: {{name}}, I’m sensing that your love life isn’t going to be the smoothest...\n5.000: But you will find that happiness that you’re looking for soon enough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n4.000: But don’t forget, {{name}}...\n1.000: How you perceive yourself should not be determined by your love life.\n5.000: Your spouse or partner is meant to play a supporting role in your life...\n4.000: Not the lead.\n1.000: Focus on your own personal growth...\n3.000: And I promise you {{name}}...\n2.000: You will soon discover heavenly rewards."

