module.exports = "1      \n\n00:01 \n\nHello {{name}},\n\n2      \n\n00:04 \n\nIf you’ve made it here, then congratulations!\n\n3\n\n00:08 \n\nBecause this means that your archetype is the Lover!\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/lover.png', fadeIn: true} {{/fn}}\n\n4\n\n00:11 \n\nBut what does this actually mean?\n\n5\n\n00:13 \n\nDon’t worry, everything you need to know will be on this page.\n\n6\n\n00:17 \n\nStick to the end of it and I promise that you’ll experience something truly magical.\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/lover.png', fadeOut: true} {{/fn}}\n\n7\n\n00:22 \n\nThe quiz you’ve just taken is designed to identify your archetype…\n\n8\n\n00:26 \n\nA personality profile that’s broken down your entire being.\n\n9\n\n00:30 \n\nYour tendencies, your strengths, your past, and your future.\n\n10\n\n00:36 \n\nYour archetype takes into account your gender, your marital status…\n\n11\n\n00:40 \n\nHow you answered the quiz, and how long you took to get those answers.\n\n12\n\n00:45 \n\nThat’s right, {{name}}.\n\n13\n\n00:46 \n\nYou can use that information to make astronomical changes in your life.\n\n14\n\n00:50 \n\nYou’ll be able to leverage on your strengths, improve on your weaknesses…\n\n15\n\n00:54 \n\nAnd embark on the life that you’ve always dreamed of.\n\n16\n\n00:58 \n\nBefore we get into that, I want to share with you about your archetype…\n\n17\n\n01:02   \n\nThe Lover, and what it means.\n\n18\n\n01:06 \n\n{{name}}, you are truly a unique individual.\n\n19\n\n01:10 \n\nAnd I genuinely believe that this world needs more people like you.\n\n20\n\n01:14 \n\nYou are a mature and deeply intelligent being…\n\n21\n\n01:18 \n\nNot necessarily in the sense of academics, but in terms of thinking differently.\n\n22\n\n01:23 \n\nWhile the world walks in one direction, you have no qualms with going against it.\n\n23\n\n01:28 \n\nThat’s what makes you so special, {{name}}.\n\n24\n\n01:32 \n\nYou’re deeply sensitive.\n\n25\n\n01:33 \n\nYou’re able to see and feel all the pains of the world.\n\n26\n\n01:37 \n\nThat’s because as a Lover, the emotions that you feel are extreme.\n\n27\n\n01:41 \n\nYour happiness is an elation beyond description…\n\n28\n\n01:46 \n\nWhile your pain feels like it can be excruciating and crippling..\n\n29\n\n01:52 \n\n{{name}}, you might not know it yet, but you are able to understand emotions at a level beyond others.\n\n30\n\n01:59 \n\nYou have an edge over everyone else when it comes to navigating your way through social situations.\n\n31\n\n02:04 \n\nIt’s no wonder that the people you encounter always feel so drawn to you.\n\n32\n\n02:09 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeIn: true} {{/fn}}\n\nThere is something else about you, {{name}}…\n\n33\n\n02:12 \n\nYou represent the empathy that the world truly needs.\n\n34\n\n02:17 \n\n{{name}}, I’m getting the sense that you’re going to live a long, and happy life.\n\n35\n\n02:23 \n\nAnd although success might not be at your feet right now…\n\n36\n\n02:25 \n\nYour riches and fulfilment are merely waiting for you.\n\n37\n\n02:30 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeOut: true} {{/fn}}\n\n{{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}\n\nBut there’s something else, {{name}}.\n\n38\n\n02:33 \n\nThere will be times when you feel as if you’re not enough for people.\n\n39\n\n02:37 \n\nBut that is far from the truth.\n\n40\n\n02:39 \n\nIn fact, you truly are a blessing to everyone who crosses your path.\n\n41\n\n02:44 \n\nYour fun and jovial nature never fails to attract amazing people to be a part of your life.\n\n42\n\n02:50 \n\nYou do, however, need to pay attention to sifting out the right people from the wrong people.\n\n43\n\n02:56 \n\nNevertheless, {{name}}, the friendships that you forge have the deepest bonds.\n\n44\n\n03:04 \n\n{{name}}, your enthusiasm for everything around you might be a little too much for others…\n\n45\n\n03:10 \n\nBut don’t let that discourage you.\n\n46\n\n03:12 \n\nThe world needs your energy and excitement.\n\n47\n\n03:15 \n\n{{name}}, you possess the uncanny ability to read social cues and see right through people.\n\n48\n\n03:22 \n\nI’m getting the sense that you can instantly tell when someone’s lying.\n\n49\n\n03:27 \n\nYet, nothing pains you more than being lied to.\n\n50\n\n03:31 \n\nYou never hesitate to jump at the opportunity to make someone’s day better.\n\n51\n\n03:36 \n\nThat’s because you thrive on both expressing and receiving love.\n\n52\n\n03:40 \n\nYou will, however, encounter wicked hearts and selfish people…\n\n53\n\n03:45 \n\nBut don’t let that lose sight of who you truly are, {{name}}.\n\n54\n\n03:51 \n\n{{name}}, I can already tell that your friends and family really look up to you.\n\n55\n\n03:56 \n\nYou’re the one who they come to for detailed advice, a listening ear, or just a quick chat.\n\n56\n\n04:02 \n\nYou’re not the type of person to have huge circles of friends.\n\n57\n\n04:06 \n\nYou’re not someone who has trouble with making friends.\n\n58\n\n04:10 \n\nIn fact, you have no trouble at all when it comes to…\n\n59\n\n04:13 \n\n…Impressing others with your intelligence and sweet nature.\n\n60\n\n04:17 \n\nBut you’re extremely selective with who you choose to let into your life.\n\n61\n\n04:21 \n\nYou prefer to keep your loved ones close, and your wolf pack small.\n\n62\n\n04:28 \n\n{{name}}, you are incredibly expressive.\n\n63\n\n04:32 \n\nYou’re fully connected to your emotions and you’re highly talented at the arts.\n\n64\n\n04:36 \n\nYou could be a masterful wordsmith or a captivating dancer.\n\n65\n\n04:41 \n\nThis makes you a natural-born artist…\n\n66\n\n04:43 \n\nBut not really in the sense of conventional art.\n\n67\n\n04:47 \n\nI sense that you will find success and fulfilment as…\n\n68\n\n04:50 \n\nA composer, choreographer, psychologist, or a trainer of any discipline.\n\n69\n\n04:59 \n\nYou can be very protective, {{name}}.\n\n70\n\n05:02 \n\nEspecially as a spouse.\n\n71\n\n05:05 \n\nYou love your significant other more than anything in the world…\n\n72\n\n05:09 \n\nWhoever you end up with or have ended up with is bound to be the luckiest person in the world.\n\n73\n\n05:14 \n\nYou are willing to give and sacrifice everything that you have for them.\n\n74\n\n05:19 \n\n{{name}}, you are going to make a lasting mark in your lifetime.\n\n75\n\n05:24 \n\nYou’re able to look at the world from an extremely detailed perspective.\n\n76\n\n05:28 \n\nYou see all the little imperfections in the world…\n\n77\n\n05:32 \n\nAnd you possess the charisma and the knowledge to make it a better place.\n\n78\n\n05:35 \n\n{{name}}, you tend to concern yourself with relationships.\n\n79\n\n05:39 \n\nYou’re at your happiest when you’re in a committed relationship…\n\n80\n\n05:42 \n\nEspecially with someone who treasures you, nurtures you, and loves you for exactly who you are.\n\n81\n\n05:50 \n\nYour faithfulness and trust are unquestionable…\n\n82\n\n05:53 \n\nBut if your trust is ever broken, you find it almost impossible to rebuild it.\n\n83\n\n06:00 \n\nAt times, you might even associate the successes and turmoils of your relationships with your self-worth.\n\n84\n\n06:08 \n\nI urge you, {{name}}, your relationships do not define you.\n\n85\n\n06:13 \n\nYou create so much value for the people around you, and the world.\n\n86\n\n06:17 \n\nYou have so much to offer.\n\n87\n\n06:20 \n\nAs you can already tell, {{name}}…\n\n88\n\n06:23 \n\nYour relationships can make you dependent on others for your own happiness.\n\n89\n\n06:27 \n\nAnd that is something that you need to be careful of.\n\n90\n\n06:31 \n\nAt times, it might feel as if you’re living your life for others.\n\n91\n\n06:35 \n\nThat’s not what you want, {{name}}.\n\n92\n\n06:37 \n\nYou want to live your life for yourself.\n\n93\n\n06:39 \n\nYou want to love yourself.\n\n94\n\n06:42 \n\nWhen you’re able to do that, success and the life of your dreams will follow.\n\n95\n\n06:47 \n\nAnd that’s what Individuation aims to teach you.\n\n96\n\n 06:53 \n\nBut there’s more to what I’ve learned about you, {{name}}…\n\n97\n\n06:57 \n\nIt feels to me that you’re a deeply emotional and spiritual person.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n\n98\n\n07:03 \n\nYou’re connected to your soul in a way that’s far more profound than others.\n\n99\n\n07:08 \n\nYour thought process does not follow the rules and logic of the world.\n\n100\n\n07:13 \n\nIn fact, I get the sense that you have quite the imagination.\n\n101\n\n07:19 \n\nYour thoughts and dreams are vivid, colourful…\n\n102\n\n07:22 \n\nAnd symbolic of who you are as an individual.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n\n103\n\n07:27 \n\n{{name}}, you’re not someone who likes to argue.\n\n104\n\n07:30 \n\nYou tend to avoid conflict and you’re uncanny when it comes to devising win-win situations.\n\n105\n\n07:36 \n\nBut when you're backed into a corner, you’re not one to back down.\n\n106\n\n07:40  \n\nYou fight fiercely for your loved ones and for righteousness.\n\n107\n\n07:45 \n\nYou’re the first person to step in when you see a fight breaking out.\n\n108\n\n07:49  \n\nYou’re the first person to reach for your pockets in the sight of a struggling busker.\n\n109\n\n07:54 \n\nYou’re the first person your friends call when they’ve had a horrible day.\n\n110\n\n07:58 \n\nYou’re the one who people rely on and look up to.\n\n111\n\n08:02 \n\n{{name}}, you are the role model of the world.\n\n112\n\n08:06 \n\nBut every lover needs a side-kick…\n\n113\n\n 08:09 \n\nA partner in crime who will stick by them when the going gets tough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n\n114\n\n08:13 \n\nThis little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n\n115\n\n08:19 \n\nOr if you’re already in a committed relationship, there’s a really good chance that your spouse is one of those archetypes.\n\n116\n\n08:26 \n\n{{name}}, you’re not the kind of person that has trouble when it comes to love.\n\n117\n\n08:32 \n\nPeople find themselves drawn to your fun-loving nature and thoughtful tendencies.\n\n118\n\n08:37 \n\nYou’re sort of a rare breed when it comes to love.\n\n119\n\n08:40 \n\nYou’re fiercely faithful, thoughtful, sweet, and emotionally connected.\n\n120\n\n08:47 \n\nThe relationships that you forge with potential lovers are always profound and deep.\n\n121\n\n08:53 \n\nYou become very selective when it comes to who you choose to date…\n\n122\n\n08:57 \n\nAnd who you choose to end up with…\n\n123\n\n09:00 \n\nFor a good reason, of course.\n\n124\n\n09:01 \n\nI sense that in the coming years, you will be the happiest you’ve ever been in that regard.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n\n125 \n\n09:07 \n\nBut don’t forget, {{name}}…\n\n126\n\n09:09 \n\nHow you perceive yourself should not be determined by your love life.\n\n127\n\n09:13  \n\nYour spouse or partner is meant to play a supporting role in your life…\n\n128\n\n09:17 \n\nNot the lead.\n\n129\n\n09:19 \n\nFocus on your own personal growth, and I promise you, {{name}}…\n\n130\n\n09:23 \n\nYou will soon discover immeasurable rewards."

