module.exports = "1 \n\n00:06 \n\nHello {{name}},\n\n2\n\n00:10 \n\nIf you’ve made it here, then congratulations!\n\n3\n\n00:13 \n\nBecause this means that your archetype is the Explorer!\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/explorer.png', fadeIn: true} {{/fn}}\n\n4\n\n00:18 \n\nBut what does this actually mean?\n\n5\n\n00:20 \n\nDon’t worry, everything you need to know will be on this page.\n\n6\n\n00:25 \n\nStick to the end of it and I promise that you’ll experience something truly magical.\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/explorer.png', fadeOut: true} {{/fn}}\n\n7\n\n00:30 \n\nThe quiz that you’ve just taken is designed to identify your archetype...\n\n8\n\n00:36 \n\nA personality profile that’s broken down your entire being.\n\n9\n\n00:40 \n\nYour tendencies, your strengths, your past, and your future.\n\n10\n\n00:45 \n\nYour archetype takes into account your gender, your marital status...\n\n11\n\n00:49 \n\nHow you answered the quiz, and how long you took to get those answers.\n\n12\n\n00:54 \n\nThat’s right, {{name}}.\n\n13\n\n00:56 \n\nYou can use that information to make astronomical changes in your life.\n\n14\n\n01:01 \n\nYou’ll be able to leverage on your strengths...\n\n15\n\n01:04 \n\nImprove on your weaknesses...\n\n16\n\n01:06 \n\nAnd embark on the life that you’ve always dreamed of.\n\n17\n\n01:09 \n\nBut before we get into that...\n\n18\n\n01:11 \n\nI want to share with you about your archetype, The Explorer, and what it means.\n\n19\n\n01:17 \n\n{{name}}, you truly are a unique individual.\n\n20\n\n01:21 \n\nAnd I genuinely believe that this world needs more people like you.\n\n21\n\n01:25 \n\nYou are a mature and a truly unconventional being.\n\n22\n\n01:28 \n\nI think the best way to describe you is “an adventurer”.\n\n23\n\n01:32 \n\nYes, you are an adventurer looking to make your mark on the world.\n\n24\n\n01:37  \n\nTo discover new lands, new opportunities, and new experiences.\n\n25\n\n01:42 \n\nYou’re different that way, {{name}}.\n\n26\n\n01:44 \n\nWhile the world walks in one direction…\n\n27\n\n01:47\n\nYou have no qualms with going against it.\n\n28\n\n01:50 \n\nThat’s what makes you so special, {{name}}.\n\n29\n\n01:53 \n\nYour biggest value is freedom.\n\n30\n\n01:56 \n\nSitting still for even just a second can make you feel jittery…\n\n31\n\n02:01 \n\n...Uneasy, restless, and just completely uncomfortable.\n\n32\n\n02:05 \n\nThat’s because as an Explorer, you are highly energetic.\n\n33\n\n02:10 \n\nWhile others struggle to feel comfortable in unfamiliar places...\n\n34\n\n02:15 \n\nYou just seem to feel at home in the most distant of lands.\n\n35\n\n02:18 \n\nThat’s because you envision the world as a singular place…\n\n36\n\n02:23 \n\nAnd you understand that each and everyone of us belong to the same home.\n\n37\n\n02:28 \n\n{{name}}, you might not know it yet...\n\n38\n\n02:30 \n\nBut you are more morally and ethically upright than others.\n\n39\n\n02:34 \n\nYou hold your values and principles close to your heart...\n\n40\n\n02:38 \n\nAnd you’re always ready to stick to your guns.\n\n41\n\n02:41  \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeIn: true} {{/fn}}\n\nThere is something else about you, {{name}}…\n\n42\n\n02:44 \n\nYou form the foundation of the world.\n\n43\n\n02:46 \n\nYou play the leading role in uncovering new discoveries.\n\n44\n\n02:51 \n\n{{name}}, I’m getting the sense that you’re going to live a long, and fruitful life.\n\n45\n\n02:56 \n\nAnd although success might not be at your feet right now...\n\n46\n\n02:59 \n\nYour riches and fulfilment are merely waiting for you.\n\n47\n\n03:03 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeOut: true} {{/fn}}\n\n{{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}\n\nBut there’s something else, {{name}}.\n\n48\n\n03:05 \n\nEven though you will triumph, your path to success will not come easy.\n\n49\n\n03:11 \n\nTalking to people is never a problem for you...\n\n50\n\n03:13 \n\nBut I’m getting the sense that you tend to struggle when it comes to...\n\n51\n\n03:17 \n\nMaintaining healthy relationships.\n\n52\n\n03:19 \n\nYour nature revolves so much around meeting new people and...\n\n53\n\n03:23 \n\nLearning about new cultures that you tend to…\n\n54\n\n03:25 \n\nForget about the people closest to you.\n\n55\n\n03:28 \n\n{{name}}, don’t let that discourage you.\n\n56\n\n03:31 \n\nThe world needs your ambition and unquenchable curiosity.\n\n57\n\n03:35 \n\nThe world needs people like you to be in positions of power.\n\n58\n\n03:40 \n\nYou are the one who keeps the world moving forward and making progress.\n\n59\n\n03:44 \n\nYou do, however, need to be less critical about yourself.\n\n60\n\n03:49 \n\nYou’re constantly pushing yourself to climb new heights and explore the world...\n\n61\n\n03:54 \n\nBut you must understand that growth takes time.\n\n62\n\n03:57 \n\nThat way, you’ll be able to be more patient with both yourself, and others.\n\n63\n\n04:03 \n\nAfter all, {{name}}, growth is a product of both effort and time...\n\n64\n\n04:08 \n\nAnd exercising patience will lead you towards the fulfilment that you seek.\n\n65\n\n04:14 \n\n{{name}}, I can already tell that your friends and family really look up to you.\n\n66\n\n04:19 \n\nYou’re the one who they come to when they’re in need...\n\n67\n\n04:22 \n\nWhen they need someone to talk to...\n\n68\n\n04:24 \n\nOr even just for a quick chat.\n\n69\n\n04:27 \n\nYou’re not the type of person to have huge circles of friends...\n\n70\n\n04:30  \n\nAlthough you’re not someone who has trouble with making friends either.\n\n71\n\n04:34 \n\nIn fact, you have no trouble at all when it comes to impressing others with...\n\n72\n\n04:39 \n\nYour worldly knowledge and accepting heart.\n\n73\n\n04:41 \n\nBut you’re extremely selective with who you choose to let into your life.\n\n74\n\n04:46 \n\nYou prefer to keep your loved ones close, and your wolf pack small.\n\n75\n\n04:50 \n\n{{name}}, you are incredibly curious...\n\n76\n\n04:53 \n\nWhich keeps conversations centered around others rather than yourself.\n\n77\n\n04:57 \n\nYou possess the unique ability to connect with others and blend into any social group.\n\n78\n\n05:03 \n\nYou have a great deal to offer to this world.\n\n79\n\n05:06 \n\n{{name}}, you are a natural-born learner, which makes you an excellent teacher.\n\n80\n\n05:12 \n\nI sense that you will find success and fulfilment as...\n\n81\n\n05:16 \n\nAn instructor, a philanthropist, a CEO, or a public relations manager.\n\n82\n\n05:21 \n\nEven though you appear to be laid back and easy going…\n\n83\n\n05:25 \n\nYou can also be very protective, {{name}}.\n\n84\n\n05:28 \n\nEspecially as a parent.\n\n85\n\n05:30 \n\nYou love your children, friends, and family fiercely.\n\n86\n\n05:34 \n\nYou are willing to give and sacrifice everything that you have for them.\n\n87\n\n05:38 \n\n{{name}}, you are going to make a lasting mark in your lifetime.\n\n88\n\n05:43 \n\nYou’re able to look at the world from a broad and altruistic perspective.\n\n89\n\n05:48 \n\nYou see the big picture, and you have what it takes to make this world more connected.\n\n90\n\n05:54 \n\nYour deep understanding of human nature and culture as a whole...\n\n91\n\n05:58 \n\nGives you extraordinary insight into how you can bring the world closer together.\n\n92\n\n06:04 \n\n{{name}}, you are practically a chameleon...\n\n93\n\n06:07 \n\nPossessing the uncanny ability to make yourself at home....\n\n94\n\n06:11 \n\nWithin any social group or social setting.\n\n95\n\n06:14 \n\nYou excel at one-on-one interactions...\n\n96\n\n06:17 \n\nAnd never fail to make an excellent impression on larger groups of people.\n\n97\n\n06:22 \n\nYour charisma and eloquence intrigues those who are fortunate enough...\n\n98\n\n06:27 \n\n...To have crossed paths with you.\n\n99\n\n06:29 \n\nUltimately, {{name}}, you are a dreamer.\n\n100\n\n06:32 \n\nYou have a strong sense of ambition...\n\n101\n\n06:35 \n\nAnd you’re willing to do whatever it takes to achieve your dreams.\n\n102\n\n06:38 \n\nI’m getting the sense that you’re not too concerned about monetary riches...\n\n103\n\n06:43 \n\nAlthough you do wish you had more wealth.\n\n104\n\n06:45 \n\nThere is, however, a greater purpose for you in this lifetime, {{name}}.\n\n105\n\n06:51 \n\n{{name}}, I understand that you tend to struggle with commitments.\n\n106\n\n06:56 \n\nAnd that is something that you need to be careful of.\n\n107\n\n06:59 \n\nAt times, it might feel as if you’re being trapped...\n\n108\n\n07:03 \n\nAnd having your freedom taken away. But that’s quite the contrary.\n\n109\n\n07:07 \n\nFulfilment ultimately comes from forging valuable relationships...\n\n110\n\n07:12 \n\nWith others, and yourself. And that requires commitment.\n\n111\n\n07:16 \n\nThat’s what you seek, {{name}}.\n\n112\n\n07:19 \n\nYou want to live life for yourself, without appearing selfish.\n\n113\n\n07:23 \n\nYou want to love yourself, while keeping the relationships you’ve forged close to your heart.\n\n114\n\n07:29 \n\nWhen you’re able to do that, success and the life of your dreams will follow.\n\n115\n\n07:34 \n\nAnd that’s what Individuation aims to teach you.\n\n116\n\n07:38 \n\nBut there’s more to what I’ve learned about you, {{name}}…\n\n117\n\n07:41 \n\nIt feels to me that you’re a deeply emotional and spiritual person.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n\n118\n\n07:46 \n\nYou’re connected to your soul in a way that’s far more profound than others.\n\n119\n\n07:51 \n\nYour thought process does not follow the rules and logic of the world.\n\n120\n\n07:55 \n\nIn fact, I get the sense that you have quite the imagination.\n\n121\n\n07:59 \n\nYour thoughts and dreams are vivid, colourful...\n\n122\n\n08:02 \n\nAnd symbolic of who you are as an individual.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n\n123\n\n08:05 \n\n{{name}}, you’re not someone who likes to argue.\n\n124\n\n08:09 \n\nYou tend to avoid conflict and you’re uncanny when it comes to devising win-win situations.\n\n125\n\n08:15 \n\nBut when backed into a corner, you’re not one to back down.\n\n126\n\n08:19 \n\nYou fight fiercely for your loved ones and for righteousness.\n\n127\n\n08:23 \n\nYou’re the first person to volunteer in the sight of disaster.\n\n128\n\n08:27 \n\nYou’re the first person to donate your last dime to someone who needs it more.\n\n129\n\n08:32 \n\nYou’re the first person to impact and change the lives of those who are less fortunate.\n\n130\n\n08:38 \n\nYou’re the one who people rely on and look up to.\n\n131\n\n08:42 \n\n{{name}}, you are the catalyst of change in humanity.\n\n132\n\n08:46 \n\nBut every explorer needs someone to accompany them on their adventures...\n\n133\n\n08:51 \n\nA partner-in-crime who will stick by them when the going gets tough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n\n134\n\n08:55 \n\nThis little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n\n135\n\n09:02 \n\nOr if you’re already in a committed relationship...\n\n136\n\n09:04 \n\nThere’s a really good chance that your spouse is one of those archetypes.\n\n137\n\n09:09 \n\n{{name}}, you’re not the kind of person that has trouble when it comes to love.\n\n138\n\n09:14 \n\nLove interests find themselves drawn to your charisma and kind nature.\n\n139\n\n09:20 \n\nYou’re sort of a rare breed when it comes to love.\n\n140\n\n09:23 \n\nYou communicate better than everyone else, and you’re a thoughtful individual.\n\n141\n\n09:28 \n\nThe relationships that you forge with potential lovers are always profound and deep.\n\n142\n\n09:33 \n\nYou become very selective with who you choose to date...\n\n143\n\n09:37 \n\nAnd who you choose to end up with.\n\n144\n\n09:39 \n\nFor good reason, of course.\n\n145\n\n09:41 \n\nI sense that in the coming years...\n\n146\n\n09:43 \n\nYou will be the happiest you’ve ever been in that regard.\n\n147\n\n09:47 \n\n{{name}}, I’m sensing that your love life isn’t going to be the smoothest...\n\n148\n\n09:52 \n\nBut you will find that happiness that you’re looking for soon enough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n\n148\n\n09:56 \n\nBut don’t forget, {{name}}...\n\n149\n\n09:57 \n\nHow you perceive yourself should not be determined by your love life.\n\n150\n\n10:02 \n\nYour spouse or partner is meant to play a supporting role in your life...\n\n151\n\n10:06 \n\nNot the lead.\n\n152\n\n10:07 \n\nFocus on your own personal growth...\n\n153\n\n10:10 \n\nAnd I promise you {{name}}...\n\n154\n\n10:12 \n\nYou will soon discover heavenly rewards."

