module.exports = "1.000:\n1.000: Okay, {{name}}…\n2.000: That’s all I can share with you for now…\n1.000: But {{name}},\n4.000: if you’ve gained something valuable from this free archetype reading,\n3.000: I want you to know that there is so much more\n2.000: that I can tell you about your archetype.\n3.000: This free reading is merely scraping the surface.\n7.000: I haven’t told you about your Shadow, your Anima/Animus, your Ego, and your life path.\n3.000: Your Shadow is the dark side of your personality\n4.000: where all of your suppressed thoughts and desires are stored.\n3.000: Your Anima and Animus represents the imbalance\n4.000: between masculinity and femininity in your character.\n3.000: Your Ego is the centre of your consciousness\n4.000: that needs to be controlled in order to find true joy.\n3.000: The Deluxe Archetype Report tells you everything\n3.000: that you need to know about all of that, and more.\n2.000: It tells you about…\n4.000: The choices that you’ve made and why you made them\n3.000: Your ideal life partner or companion\n4.000: Your path towards unlocking enormous amounts of success\n3.000: The challenges that lie ahead of you\n4.000: Your path towards finding fulfilment in this lifetime\n6.000: The Deluxe Archetype Report is a reading that’s far more detailed, and far more intimate.\n3.000: If you thought that this free archetype report was accurate,\n2.000: you’re about to have your mind blown.\n6.000: In this reading, you will discover who you are. Yes, who you REALLY are.\n5.000: It’s going to be a completely raw and uncensored look into your personality.\n5.000: All of this information will be presented to you in excruciating detail.\n5.000: If you’re like most people, you’ll find that it’s an extremely emotional read.\n7.000: Imagine the most emotional film you’ve ever seen, and multiply that emotion by ten times.\n6.000: You’ll find yourself identifying with every single characteristic of your personality.\n5.000: These are things that not even your closest friends or family know about you.\n4.000: You might not even have realized some of these things yourself.\n3.000: You will get emotional when you discover the reason for the\n3.000: trials and misfortunes that you’ve been through.\n4.000: You will confront the struggles that you’ve been in constant battle with.\n7.000: You will get emotional when you learn about the amazing, wonderful gifts that life has in store for you.\n8.000: You will pick up, examine, and embrace every single piece of your character, personality, and entire being.\n5.000: You will place everything back together and witness the beauty that it all forms.\n4.000: You will master your weaknesses and learn how to improve them.\n5.000: You will learn about your strengths and discover how to leverage on them.\n3.000: At the end of this detailed report,\n4.000: you’ll take everything that you’ve experienced and take advantage of them.\n5.000: The answers to all of your life’s questions, to all of your life’s complications,\n4.000: to all of your life’s encounters, failures, and successes,\n4.000: are waiting for you in your Deluxe Archetype Report.\n3.000: If you’re unsure of the path that you’re currently on,\n5.000: this report will tell you everything you need to know about where to go NEXT.\n3.000: If you’re wondering whether your actions are right or wrong,\n4.000: this report will grant you unquestionable direction.\n6.000: If the people around you are holding you back, this report will give you all the clarity you need.\n3.000: Everything I’ve just talked about is just waiting for you.\n6.000: And the only thing standing between you and the life you’ve always dreamed of is YOURSELF.\n6.000: Tell me, do you want to be so successful that you won’t have to worry about money any more?\n2.000: Do you want to make the most meaningful relationships?\n4.000: Do you want to have good fortune in everything that you do?\n5.000: Most importantly, do you want to live the life that you’ve always dreamed of?\n5.000: The Deluxe Archetype Report WILL give you all of that on a silver platter.\n5.000: Unfortunately, I can’t lead you through this journey if you don’t let me.\n2.000: You need to take that first step.\n4.000: You need to trust your instincts and take that leap of faith…\n5.000: If you’re worrying about your career, your relationships, your health, your finances…\n8.000: If you haven’t the slightest clue of where you’re going to be 10, 5, 3 or even 1 year from now…\n3.000: If you want to become a better version of yourself…\n5.000: If you want to take back the reigns and choose the direction of your life…\n4.000: All of the answers are waiting for you in your Deluxe Archetype Report.\n2.000: To make things a little easier for you,\n4.000: I’ve simplified everything for you to make the right choice.\n5.000: Here’s what the Deluxe Archetype Report will almost instantly accomplish for you:\n5.000: You’ll unlock the deepest desires of your heart and soul; desires\n5.000: that you would never expect, and exactly how you can go about achieving them.\n4.000: You’ll realize opportunities before they’ve even presented themselves.\n4.000: You’ll recognize potential pitfalls from miles away and\n4.000: learn how you can protect yourself or even avoid them completely.\n5.000: You’ll be spoon-fed the necessary knowledge and tools to achieve what you truly want.\n3.000: You’ll achieve the life that you’ve always wanted;\n4.000: the life that you’ve wanted for the longest time.\n4.000: And finally, you’ll learn how you can make the absolute best of it!\n2.000: But that’s not even the half of it.\n2.000: In just a minute,\n7.000: I’m going to share with you even more amazing ways that will help you achieve the life that you truly want.\n2.000: Before we get into that,\n2.000: Allow me to share this important secret with you.\n2.000: Now, pay close attention.\n6.000: Individuation is based on thousands of years of in-depth analysis and research.\n8.000: Famous philosophers and psychologists have improved and enhanced the principles of individuation for that long.\n1.000: You need specifics?\n4.000: Individuation has been present during the Renaissance,\n6.000: during the Medieval era, and dates all the way back to 350BC (Aristotle).\n3.000: And because of the technology that we have access to today,\n7.000: we can achieve the same success that TENS OF THOUSANDS of ordinary people have experienced with individuation.\n2.000: The best part?\n4.000: You can uncover these hidden secrets with a simple click of a button.\n6.000: I’ve dedicated more than 10 years of my life towards researching and studying individuation.\n5.000: And I can guarantee that’s what I’ll be doing for the rest of my life.\n2.000: That’s a fact.\n3.000: And here’s something else that’s going to blow your mind…\n1.000: Are you ready for this?\n10.000: The Deluxe Archetype Report is EASILY the most DETAILED, ACCURATE, and REVEALING individuation report in the ENTIRE WORLD.\n4.000: We have dedicated decades, including 10 years from myself,\n6.000: to creating a report that’s as detailed and accurate as the Deluxe Archetype Report.\n5.000: Every single word has been handwritten, edited, and re-edited,\n3.000: to ensure that this report contains ZERO FLUFF\n4.000: and only the MOST BENEFICIAL information about you.\n5.000: Years of thought have gone into every single sentence.\n6.000: Years of constant editing and corrections have gone into perfecting each paragraph.\n4.000: Years of research have gone into the contents of this report.\n5.000: It’s been completely personalized for you, and only you, {{name}}.\n5.000: No one else in the world can pick it up and benefit from it in the same way that you will.\n4.000: Embarking on this journey WILL open doors for you\n4.000: that you would have never discovered without taking this first step.\n3.000: It’s an eye-opening experience like no other.\n5.000: The contents of this report can never be found anywhere else.\n4.000: It hasn’t been published or even copied. NOT ONCE.\n4.000: {{name}}, this is the one and only place you’ll be able to get your hands on it.\n6.000: Everything that you read from this report is completely tailored to your main archetype.\n5.000: It will give you the EXACT system that over 70,000 ordinary people,\n5.000: including myself, have used to completely CHANGE OUR LIVES.\n2.000: It’s entirely unique to you.\n6.000: And that’s what individuation is all about – discovering your true self.\n4.000: I have no doubt in my mind that you will be absolutely stunned\n3.000: as you read about your past, your present, and your future.\n6.000: And at the end of it all, you will be RAVING about its degree of ACCURACY.\n5.000: But more importantly, you will experience significant improvements in your daily life\n4.000: after you apply the techniques and the knowledge that you learn from this report.\n9.000: Personally, I get emails every single day from people sharing their successes after receiving their Deluxe Archetype Report from me.\n6.000: But I’m not done just yet. I’m extremely passionate about individuation.\n6.000: And that’s precisely why I want you to experience the real value of individuation.\n4.000: To ensure that you gain the maximum value from this report,\n5.000: I’ll be sending you THREE additional FREE reports along with it…\n4.000: All of this on top of your Deluxe Archetype Report.\n3.000: That’s how much I believe in this product.\n7.000: Like your Deluxe Archetype Report, these additional reports are easy to understand and extremely valuable.\n2.000: {{name}}, You will receive:{{#fn}} {fn: 'sliderImageSingle', path:'images/pop-up/dar-priceless.png', fadeIn: true} {{/fn}}\n4.000: The Exploring Your Birthdate with The Chinese Zodiac eBook,\n3.000: The Discovering Your Aura eBook,\n3.000: and The Beginners Guide to the Fengshui Paradigm eBook\n3.000: I’ll be completely honest with you.\n5.000: The total value of all of these products is at least $244.00,\n4.000: and that’s practically pennies compared to how much value you’ll receive from them!{{#fn}} {fn: 'sliderImageSingle', path:'images/pop-up/dar-priceless.png', fadeOut: true} {{/fn}}\n4.000: After all, you can’t put a dollar figure on improving your life –\n4.000: because such a gift would be practically priceless.\n5.000: Money can always be earned, and there’s only so much time we have left to live.\n5.000: But no {{name}}, I’m NOT going to ask you for that amount of money.\n3.000: I’m not even going to ask you for $200\\.\n4.000: Well, what about $100? Nope…\n2.000: Well, but what about $50?\n2.000: That seems like a pretty fair price.\n3.000: But no, I’m not even going to ask you for that much.\n2.000: So, here’s what we’re going to do.\n4.000: I’m going to cut you an exclusive, one-time deal.{{#fn}} {fn: 'sliderImageSingle', path:'images/deluxe/deluxe-books-flat-additional.png', fadeIn: true} {{/fn}}\n6.000: You’ll get the full package, including the Deluxe Archetype Report, for only $37.\n2.000: That’s it.\n3.000: But I want you to share with me your success story.\n4.000: I read every single one of my emails and respond to them personally,\n4.000: and I want to read about your experiences too.\n3.000: We need the feedback to keep on improving our products,\n4.000: and it definitely encourages us to keep doing what we do.\n5.000: The Deluxe Archetype Report is all yours for just $37.{{#fn}} {fn: 'sliderImageSingle', path:'images/deluxe/deluxe-books-flat-additional.png', fadeOut: true} {{/fn}}\n2.000: Now, you have two options.\n3.000: You can continue on the life path that you’re on right now\n5.000: and stay exactly the way that you are, unaware of your life’s purpose,\n3.000: your calling, and your true personality.\n8.000: Or, you can have your life’s purpose, your calling, your true personality, and your life’s direction revealed to you\n5.000: AND experience an abundance of love, health, and wealth in your life.\n5.000: All you have to do is click the button below and your Deluxe Archetype Report,\n4.000: and all 3 FREE bonus reports, will be sent to you INSTANTLY.\n6.000: But that’s not the best that I can do. I know I can do better than that.\n4.000: On top of giving away the whole package for just $37,\n4.000: I’m also going to make the following guarantee for your own personal protection:{{#fn}} {fn: 'sliderImageSingle', path:'images/pop-up/60-day-money-back-guarantee.png', fadeIn: true} {{/fn}}\n6.000: {{name}},take your time to look through your Deluxe Archetype Report and everything else that comes with it.\n7.000: If you decide within the next 60 days that you’re not completely satisfied with your Deluxe Archetype Report,\n7.000: Just drop us an e-mail at individualogist.com and we’ll issue you a full refund.\n3.000: No questions and no explanations will be necessary.\n2.000: I’m making this guarantee\n8.000: Because I’m 100% certain that this report has the capacity to truly turn your life around.\n3.000: That’s how much I believe in the process of individuation,\n4.000: and that’s how much I believe you will benefit from it.{{#fn}} {fn: 'sliderImageSingle', path:'images/pop-up/60-day-money-back-guarantee.png', fadeOut: true} {{/fn}}\n4.000: {{name}}, so, no matter what, you’ve got the longer end of the stick.\n4.000: There is absolutely no risk involved, and that’s all up to you\n3.000: and whether you decide to take this life-changing path.\n6.000: When you click on the button shortly, you’ll be taken to a 100% secure order page.\n3.000: After filling out a few questions,\n5.000: you’ll have the Deluxe Archetype Report and the rest of the reports sent directly to you instantly.\n5.000: You won’t have to wait a second longer to begin your individuation journey.\n9.000: There’s not much time left, because this unique offer is only going to be extended to you just once and, only once.\n8.000: To receive your Deluxe Archetype Report, and the 3 additional bonuses, at the once in a life time price of only $37,\n2.000: You need to act right now.\n5.000: Just click the button below to experience a real change in your life.\n6.000: I’m offering it to you right now for just $37, but {{name}}, you need to make the decision NOW.\n6.000: People approach me constantly with concerns about their careers, and I’ve used the same information\n6.000: and techniques in this package to help them SKYROCKET their SUCCESS by TEN FOLDS.\n6.000: Married couples on the verge of separation have approached me for the secrets of individuation as well,\n1.000: and guess what?\n4.000: They’re now enjoying the most rewarding relationships.\n6.000: All of this is made possible because of this EXCLUSIVE Deluxe Archetype Report Package.\n4.000: I can go on for days about all of the success stories that\n4.000: others have experienced after receiving the Deluxe Archetype Report.\n6.000: But the truth is, I honestly want you, {{name}}, to be one of the success stories that I share with people.\n5.000: When I embarked on this journey, there was no one around to help me.\n3.000: In fact, we didn’t even have the Internet back then.\n6.000: The only thing I could do was sit in that library for hours and hours every single day,\n4.000: finding every book that I could about individuation.\n3.000: I read every page and every word.\n8.000: To date, I’ve probably gone through over hundreds of thousands of pages and thousands of books and materials on individuation.\n7.000: I’m that passionate about it, and that’s why I want to share this gift with you now.\n3.000: Applying individuation is what truly saved me.\n5.000: It’s what turned my life around, and it’s what helped me to find joy.\n7.000: Not many of us are lucky enough to have experienced true joy, but that’s what I’m going to help you find.\n3.000: Today, you’ve stumbled upon the rare opportunity.\n5.000: The opportunity for you to completely change your life.\n4.000: This might be the one and only time that life hands you a reset button.\n6.000: This is your chance to discover your true self and unlock a massive amount of benefits\n2.000: that will change your life for the better.\n5.000: I’m more than happy to make this one-time offer to you, but just like you,\n3.000: I only have 24 hours in a day.\n5.000: If you don’t respond now, I’ll have no choice but to invest my time in people\n4.000: who have already taken advantage of this once in a lifetime opportunity.\n4.000: And sadly enough, your opportunity would have been lost.\n5.000: Not many people in the world will have the opportunity that you have right now {{name}}.\n5.000: This is the opportunity where your life can REALLY turn around.\n4.000: {{name}}, this is your time to shine and make the decision\n3.000: that will align your direction with your destiny.\n5.000: The thing is, you could probably find a book about Individuation from a local book store,\n4.000: which will contain a chapter or a couple of pages about your archetype.\n7.000: But that single chapter or couple of pages isn’t going to be very helpful. So, here’s the truth.\n6.000: {{name}}, The Deluxe Archetype Report is an excruciatingly detailed report about your archetype,\n8.000: containing 200 highly informative pages of actionable and thought-provoking strategies and methodologies.\n6.000: Once again, all of the information is customized to your specific archetype.\n6.000: It breaks down an in-depth outlook of your life journey and all of its obstacles.\n6.000: There is no other product that comes as close in terms of detail and information.\n5.000: The Deluxe Archetype Report is 100% owned and authored by us. {{name}},\n5.000: this is the only place where you’re able to get it, or anything like it.\n5.000: And if you’ve tried similar products that didn’t help before, here’s the difference.\n6.000: {{name}}, other products assume that everyone functions and processes information in the same way.\n5.000: The law of attraction assumes that everyone is built with the same framework.\n4.000: Manifestation programs assume that everyone will respond the same way.\n6.000: Meditation methods assume that everyone experiences calmness similarly.\n5.000: None of that can be true, because, we are all different individuals.\n5.000: We each have different fears, different circumstances, and different life experiences.\n5.000: Individuation and archetypes take that into account.\n4.000: Our readings are specifically catered to your archetype to provide you\n5.000: with actionable steps that you can take towards making a true change in your life.\n4.000: It reveals callings and directions that are specific to you.\n3.000: That’s why it’s going to work.\n5.000: This product seeks to bring the term “personal” back into personal development;\n3.000: an industry littered with impersonal products\n4.000: that have strayed away from what personal development is meant to be.\n6.000: But beyond that, the Deluxe Archetype Report is proven and supported by science.\n8.000: Individuation is a psychological process that was created by thought leaders in the psychological world decades ago.\n6.000: In fact, principles of Individuation can be traced all the way back to the days of Aristotle.\n8.000: This means that Individuation has stood the test of time to be a proven personal development process.\n8.000: There have also been numerous conclusive scientific studies about analytical psychology and individuation across the globe.{{#fn}} {fn: 'sliderImageSingle', path:'images/deluxe/deluxe-books-flat-additional.png', fadeIn: true} {{/fn}}\n6.000: Unlock your Deluxe Archetype Report and the full package of 3 FREE reports now,\n3.000: before they’re no longer available to you!\n2.000: All you have to do is click the button below {{name}},\n5.000: and you’ll receive the entire Deluxe Archetype Report package immediately.\n3.000: The time has come for you to take action.\n3.000: Now, let’s get this show on the road.\n1.000: We’ll speak again soon, and that’s a promise."

