module.exports = "2.000:\n2.000: Hello {{name}},\n4.000: If you’ve made it here, then congratulations!\n3.000: Because this means that your archetype is the Sage!\n3.000: But what does this actually mean?{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/sage.png', fadeIn: true} {{/fn}}\n4.000: Don’t worry, everything you need to know will be on this page.\n3.000: Stick to the end of it and I promise that\n3.000: you’ll experience something truly magical.\n5.000: The quiz that you’ve just taken is designed to identify your archetype\n5.000: a personality profile that’s broken down your entire being.{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/sage.png', fadeOut: true} {{/fn}}\n5.000: Your tendencies, your strengths, your past, and your future.\n3.000: Your archetype takes into account your gender,\n4.000: your marital status, how you answered the quiz,\n3.000: and how long you took to get those answers.\n2.000: That’s right, {{name}}.\n4.000: You can use that information to make astronomical changes in your life.\n5.000: You’ll be able to leverage on your strengths, improve on your weaknesses,\n3.000: and embark on the life that you’ve always dreamed of.\n6.000: But before we get into that, I want to share with you about your archetype, The Sage,\n2.000: and what it means.\n3.000: {{name}}, you truly are a unique individual.\n4.000: And I genuinely believe that this world needs more people like you.\n5.000: You are a wise soul that’s overflowing with knowledge and wisdom.\n7.000: If you were a vehicle, knowledge and wisdom would be your fuel – it’s what drives you.\n2.000: You’re different that way, {{name}}.\n6.000: While the world walks in one direction, you have no qualms with going against it.\n4.000: That’s what makes you so special, {{name}}.\n4.000: You’re deeply intuitive and introspective.\n6.000: Unlike others, your education doesn’t stop after graduating from college or university.\n4.000: You are constantly applying and enriching yourself throughout the course of your life.\n2.000: {{name}}, you might not know it yet,\n4.000: but you are more morally and ethically upright than others.\n3.000: You hold your values and principles close to your heart,\n3.000: and you’re always ready to stick to your guns.{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeIn: true} {{/fn}}\n2.000: There is something else about you, {{name}}…\n6.000: You form the foundation of the world’s progress and advancement.{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeOut: true} {{/fn}}\n4.000: {{name}}, I’m getting the sense that you’re going to live a long, and happy life.\n3.000: And although success might not be at your feet right now,\n3.000: your riches and fulfilment are merely waiting for you.\n3.000: {{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}.\n5.000: Even though you will triumph, your path to success will not come easy.\n4.000: You must ultimately defeat and control your hatred for ignorance.\n5.000: It is vital for you to understand that not everyone is capable of learning at your pace,\n4.000: and not everyone shares the same passion as you do for knowledge.\n2.000: {{name}}, don’t let that discourage you.\n2.000: The world needs your wisdom.\n4.000: The world needs people like you to be in positions of power.\n3.000: You keep the world moving forward.\n4.000: You do, however, need to understand that it’s important for you\n3.000: to exercise humility in both your thoughts and actions.\n5.000: That way, you’ll be able to build trust and create even more fruitful interactions.\n4.000: After all, {{name}}, you tend to struggle when it comes to taking action\n4.000: and humility will lead you down the road of embracing failure,\n2.000: which will then lead you to the path towards success.\n5.000: {{name}}, I can already tell that your friends and family really look up to you.\n4.000: You’re the one who they come to for detailed advice, a listening ear,\n2.000: or just for a quick chat.\n4.000: You’re not the type of person to have huge circles of friends.\n3.000: You’re not someone who has trouble with making friends.\n4.000: In fact, you have no trouble at all when it comes to impressing others\n2.000: with your intelligence and wisdom.\n4.000: But you’re extremely selective with who you choose to let into your life.\n4.000: You prefer to keep your loved ones close, and your wolf pack small.\n4.000: {{name}}, you are incredibly nurturing.\n3.000: You possess the unique ability to connect with others\n3.000: and blend into any social group.\n2.000: You have a great deal to offer to this world. {{name}},\n5.000: no one is able to play the role of an advisor and guide as excellently as you.\n4.000: I sense that you will find success and fulfilment\n5.000: as a business advisor, a researcher, a data scientist, or a consultant.\n3.000: You can be very intuitive, {{name}}.\n6.000: Despite your great capacity for knowledge, you prioritize your intuition over facts,\n3.000: especially when it comes to making important decisions.\n2.000: And that’s what works best for you.\n5.000: {{name}}, you are going to make a lasting mark in your lifetime.\n4.000: You’re able to look at the world from a broad and altruistic perspective.\n3.000: You see the big picture, and you have what it takes to\n3.000: make this world a richer and more informed place for everyone.\n3.000: {{name}}, you are practically a bookworm\n5.000: – possessing the uncanny ability to bury yourself with endless readings.\n5.000: You take an interest in a broad number of subjects, and your fulfilment is derived from learning.\n3.000: You excel at one-on-one interactions,\n4.000: and never fail to make an excellent impression on others during such situations.\n4.000: However, it is vital for you to understand that your introverted nature\n3.000: makes you struggle to speak to larger groups of people.\n6.000: Ultimately, {{name}}, you are a realist. You have a clear conscience.\n4.000: Discerning right from wrong has never been a difficult task for you.\n4.000: I’m getting the sense that you’re not too concerned about monetary riches,\n3.000: although you do wish you had more wealth.\n3.000: There is, however, a greater purpose for you in this lifetime, {{name}}.\n4.000: However, {{name}}, your purpose and direction in life might feel empty at times.\n3.000: And that is something that you need to be careful of.\n4.000: You might feel as if you’re living your life for the sake of learning,\n4.000: and you continue doing what you do because it’s the only thing you know how to do.\n8.000: That’s not what you want, {{name}}. You want to live life for yourself. You want to love yourself.\n5.000: When you’re able to do that, success and the life of your dreams will follow.\n3.000: And that’s what Individuation aims to teach you.\n4.000: But there’s more to what I’ve learned about you, {{name}}…\n4.000: It feels to me that you’re a deeply emotional and spiritual person.\n5.000: You’re connected to your soul in a way that’s far more profound than others.{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n4.000: Your thought process does not follow the rules and logic of the world.\n4.000: In fact, I get the sense that you have quite the imagination.\n6.000: Your thoughts and dreams are vivid, colourful, and symbolic of who you are as an individual.{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n4.000: {{name}}, you’re not someone who likes to argue.\n3.000: You tend to avoid conflict and you’re uncanny\n3.000: when it comes to devising win-win situations.\n4.000: But when backed into a corner, you’re not one to back down.\n4.000: You fight fiercely for your loved ones and for your righteousness.\n4.000: You’re the first person to step in when you see a fight breaking out.\n5.000: You’re the first person to reach for your pockets in the sight of a struggling busker.\n4.000: You’re the first person your friends call when they’ve had a horrible day.\n3.000: You’re the one who people rely on and look up to.\n3.000: {{name}}, you are the guardian of the world.\n7.000: But every superhero needs a side-kick; a partner in crime who will stick by them when the going gets tough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n4.000: This little chart I’ve prepared represents the archetypes\n2.000: that you’re most likely to end up with.\n2.000: Or if you’re already in a committed relationship,\n4.000: there’s a really good chance that your spouse is one of those archetypes.\n5.000: {{name}}, you’re not the kind of person that has trouble when it comes to love.\n4.000: People find themselves drawn to your charisma and kind nature.\n3.000: You’re sort of a rare breed when it comes to love.\n2.000: You communicate better than anyone else,\n3.000: and you’re an incredibly thoughtful individual.\n4.000: The relationships that you forge with potential lovers are always profound and deep.\n4.000: You become very selective with who you choose to date\n4.000: and who you choose to end up with – for good reason, of course.\n5.000: I sense that in the coming years, you will be the happiest you’ve ever been in that regard.{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n4.000: {{name}}, I’m sensing that your love life isn’t going to be the smoothest\n4.000: – but you will find that happiness that you’re looking for soon enough.\n7.000: But don’t forget, {{name}}, how you perceive yourself should not be determined by your love life.\n5.000: Your spouse or partner is meant to play a supporting role in your life; not the lead.\n4.000: Focus on your own personal growth, and I promise you,\n1.000: {{name}}, you will soon discover heavenly rewards."

