module.exports = "1      \n\n00:02 \n\nHello {{name}},\n\n2      \n\n00:04 \n\nIf you’ve made it here, then congratulations!\n\n3\n\n00:07 \n\nBecause this means that your archetype is the Innocent!\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/innocent.png', fadeIn: true} {{/fn}}\n\n4      \n\n00:11 \n\nBut what does this actually mean?\n\n5\n\n00:13 \n\nDon’t worry, everything you need to know will be on this page.\n\n6\n\n00:17 \n\nStick to the end of it and I promise that you’ll experience something truly magical.    \n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/innocent.png', fadeOut: true} {{/fn}}\n\n7      \n\n00:23 \n\nThe quiz that you’ve just taken is designed to identify your archetype…\n\n8\n\n00:28 \n\nA personality profile that’s broken down your entire being.\n\n9\n\n00:32 \n\nYour tendencies, your strengths, your past, and your future.\n\n10\n\n00:37 \n\nYour archetype takes into account your gender, your marital status…\n\n11\n\n00:42 \n\nHow you answered the quiz, and how long you took to get those answers.\n\n12\n\n00:47 \n\nThat’s right, {{name}}.\n\n13\n\n00:49 \n\nYou can use that information to make astronomical changes in your life.\n\n14\n\n00:54 \n\nYou’ll be able to leverage on your strengths, improve on your weaknesses…\n\n15\n\n00:58 \n\nAnd embark on the life you’ve always dreamed of.\n\n16\n\n01:03 \n\nBut before we get into that, I want to share with you about your archetype…\n\n17\n\n01:08 \n\nThe Innocent, and what it means.\n\n18\n\n01:11 \n\n{{name}}, you are truly a unique individual.\n\n19\n\n01:15 \n\nAnd I genuinely believe that this world needs more people like you.\n\n20\n\n01:20 \n\nYou are an extremely positive and optimistic being.\n\n21\n\n01:23 \n\nYou are capable of finding joy in the smallest of things, and you always have the purest of intentions.\n\n22\n\n01:30 \n\nYou’re different that way, {{name}}.\n\n23\n\n01:32 \n\nWhile the world walks in one direction, you have no qualms with going against it.\n\n24\n\n01:38 \n\nThat’s what makes you so special, {{name}}.\n\n25\n\n01:42 \n\nYou’re deeply sensitive.\n\n26\n\n01:44 \n\nYou’re able to empathize with anyone and everyone.\n\n27\n\n01:48 \n\nThat’s because as an Innocent, you are highly compassionate.\n\n28\n\n01:52 \n\nYou put the needs of others before your own…\n\n29\n\n01:55 \n\nAnd you’re able to connect with strangers just as well as you’re able to connect with your closest loved ones.\n\n30\n\n02:02 \n\nBut beyond that, you are also an incredibly strong and dedicated individual.\n\n31\n\n02:08 \n\n{{name}}, you might not know it yet…\n\n32\n\n02:11 \n\nBut you are more morally and ethically upright than others.\n\n33\n\n02:15 \n\nYou hold your values and principles close to your heart, and you’re always ready to stick to your guns.\n\n34\n\n02:22 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeIn: true} {{/fn}}\n\nThere is something else about you, {{name}}…\n\n35\n\n02:25 \n\nYou represent what it means to be strong.\n\n36\n\n02:29 \n\n{{name}}, I’m getting the sense that you’re going to live a long, and abundant life.\n\n37\n\n02:35 \n\nAnd although success might not be at your feet right now, your riches and fulfilment are merely waiting for you.\n\n38\n\n02:42 \n\n{{#fn}} {fn: 'sliderImageGlobe', fadeOut: true} {{/fn}}\n\n{{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}\n\nBut there’s something else, {{name}}.\n\n39\n\n02:44 \n\nEven though you will triumph, your path to success will not come easy.\n\n40\n\n02:49 \n\nHowever, I have no doubt that you will effortlessly power through the darkest of days.\n\n41\n\n02:55 \n\nThere is no one else in the world who’s able to match your spiritual and mental strength.\n\n42\n\n03:00 \n\nYour faith is truly unbreakable.\n\n43\n\n03:06 \n\n{{name}}, don’t let the judgments of the world discourage you.\n\n44\n\n03:11 \n\nThe world needs your selflessness and strong nature.\n\n45\n\n03:15 \n\nThe world needs people like you to be in positions of power.\n\n46\n\n03:19 \n\nYou play a fundamental role in the world, representing the importance of self-belief and perseverance.\n\n47\n\n03:25 \n\nYou do, however, need to understand that it’s easy for you to stay within your comfort zone.\n\n48\n\n03:31 \n\nInstead, you should be constantly jumping into new opportunities and discovering your path as it unfolds.\n\n49\n\n03:39 \n\nThat way, you’ll be able to continue to grow as a person, as an individual, and as an Innocent.\n\n50\n\n03:46 \n\nAfter all, {{name}}, your personal growth will ultimately determine your path to fulfilment.\n\n51\n\n03:53 \n\nOf course, I have no doubt that you will make it there.\n\n52\n\n03:57 \n\n{{name}}, I can already tell that your friends and family really look up to you.\n\n53\n\n04:03 \n\nYou’re the one who they come to for detailed advice, a listening ear, or just a quick chat.\n\n54\n\n04:10 \n\nYou’re not the type of person to have huge circles of friends.\n\n55\n\n04:14 \n\nYou’re not someone who has trouble with making friends.\n\n56\n\n04:17 \n\nIn fact, you have no trouble at all when it comes to impressing others with your kind and sweet nature.\n\n57\n\n04:26 \n\nYou tend to be more accepting of others, allowing you to forge a great number of friendships.\n\n58\n\n04:32 \n\nNevertheless, you prefer to keep your loved ones close, and your wolf pack small.\n\n59\n\n04:37 \n\n{{name}}, you are incredibly nurturing.\n\n60\n\n04:42  \n\nYou possess the unique ability to connect with others and blend into any social group.\n\n61\n\n04:47 \n\nYou have a great deal to offer to this world.\n\n62\n\n04:50 \n\nFriend, you are a natural-born leader.\n\n63\n\n04:53 \n\nI sense that you will find success and fulfilment as…\n\n64\n\n04:56 \n\nA nurse, a motivational speaker, a therapist, or a businessperson.\n\n65\n\n05:04 \n\nYou can be very protective, {{name}}.\n\n66\n\n05:06 \n\nEspecially as a parent.\n\n67\n\n05:09 \n\nYou love your children, friends, and family fiercely.\n\n68\n\n05:13 \n\nYou are willing to give and sacrifice everything that you have for them.\n\n69\n\n05:17 \n\n{{name}}, you are going to make a lasting mark in your lifetime.\n\n70\n\n05:22 \n\nYou’re able to look at the world from a broad and altruistic perspective.\n\n71\n\n05:26 \n\nYou see the importance of personal growth, and…\n\n72\n\n05:29 \n\nYou have what it takes to make a massive amount of impact in the world.\n\n73\n\n05:33 \n\n{{name}}, you are on a quest – a quest for happiness.\n\n74\n\n05:39 \n\nI’m sensing that you’ve been struggling with finding fulfilment.\n\n75\n\n05:43 \n\nPlease allow me to say this…\n\n76\n\n05:45 \n\nThe light at the end of the tunnel is approaching…\n\n77\n\n05:48 \n\nAnd you will find your fulfilment a lot sooner than you expected.\n\n78\n\n05:52 \n\nUltimately, {{name}}, you are a realist.\n\n79\n\n05:56 \n\nYou have a clear conscience.\n\n80\n\n05:58 \n\nDiscerning right from wrong has never been a difficult task for you.\n\n81\n\n06:02 \n\nI’m getting the sense that you’re not too concerned about monetary riches…\n\n82\n\n06:06 \n\nAlthough you do wish you had more wealth.\n\n83\n\n06:09 \n\nThere is, however, a greater purpose for you in this lifetime, {{name}}.\n\n84\n\n06:14 \n\nHowever, {{name}}, I’m sensing that you’re afraid of failure.\n\n85\n\n06:20 \n\nBut you should’t be afraid of anything at all.\n\n86\n\n06:23 \n\nYour positivity and strength form the foundation of success.\n\n87\n\n06:27 \n\nAs long as you continue to act on new opportunities for personal growth…\n\n88\n\n06:31 \n\nYou will find the success that you’re looking for.\n\n89\n\n06:35 \n\nAt times, it might feel as if you’re living your life for others.\n\n90\n\n06:41 \n\nThat’s not what you want, {{name}}.\n\n91\n\n06:43 \n\nYou want to live life for yourself.\n\n92\n\n06:46 \n\nYou want to love yourself.\n\n93\n\n06:48 \n\nWhen you’re able to do that, success and the life of your dreams will follow.\n\n94\n\n06:54 \n\nAnd that’s what Individuation aims to teach you.\n\n95\n\n06:58 \n\nThere’s more to what I’ve learned about you, {{name}}…\n\n96\n\n07:03 \n\nIt feels to me that you’re a deeply emotional and spiritual person.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n\n97\n\n07:08 \n\nYou’re connected to your soul in a way that’s far more profound than others.\n\n98\n\n07:13 \n\nYour thought process does not follow the rules and logic of the world.\n\n99\n\n07:17 \n\nIn fact, I get the sense that you have quite the imagination.\n\n100\n\n07:23 \n\nYour thoughts and dreams are vivid, colourful, and…\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n\n101\n\n07:27 \n\nSymbolic of who you are as an individual.\n\n102\n\n07:31 \n\n{{name}}, you’re not someone who likes to argue.\n\n103\n\n07:34 \n\nYou tend to avoid conflict and you’re uncanny when it comes to devising win-win situations.\n\n104\n\n07:41 \n\nBut when backed into a corner, you’re not one to back down.\n\n105\n\n07:44 \n\nYou fight fiercely for your loved ones and for righteousness.\n\n106\n\n07:48 \n\nYou’re the first person to step in when you see a fight breaking out.\n\n107\n\n07:52 \n\nYou’re the first person to reach for your pockets in the sight of a struggling busker.\n\n108\n\n07:56 \n\nYou’re the first person your friends call when they’ve had a horrible day.\n\n109\n\n08:01 \n\nYou’re the one who people rely on and look up to.\n\n110\n\n08:07 \n\nYou might not know this, but the people around you draw inspiration…\n\n111\n\n08:12 \n\n…Motivation, and strength from your energy.\n\n112\n\n08:15  \n\nYour purity is unparalleled.\n\n113\n\n08:18 \n\n{{name}}, you are the angel overlooking the goodness of the world.\n\n114\n\n08:23 \n\nBut even angels need someone to rely on…\n\n115\n\n08:27 \n\nA partner in crime who will stick by them when the going gets tough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n\n116\n\n08:31 \n\nThis little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n\n117\n\n08:37 \n\nOr if you’re already in a committed relationship…\n\n118\n\n08:39 \n\nThere’s a really good chance that your spouse is one of those archetypes.\n\n119\n\n08:44 \n\n{{name}}, you’re not the kind of person that has trouble when it comes to love.\n\n120\n\n08:48 \n\nPeople find themselves drawn to your charisma and kind nature.\n\n121\n\n08:53 \n\nYou’re sort of a rare breed when it comes to love.\n\n122\n\n08:57  \n\nYou communicate better than anyone else, and you’re an incredibly thoughtful individual.\n\n123\n\n09:02 \n\nThe relationships that you forge with potential lovers are always profound and deep.\n\n124\n\n09:09 \n\nYou become very selective when it comes to who you choose to date…\n\n125\n\n09:13 \n\nAnd who you choose to end up with.\n\n126\n\n09:15 \n\nFor a good reason, of course.\n\n127\n\n09:17 \n\nI sense that in the coming years, you will be the happiest you’ve ever been in that regard.\n\n128\n\n09:23 \n\n{{name}}, I’m sensing that your love life isn’t going to be the smoothest…\n\n129\n\n09:28 \n\nBut you will find that happiness that you’re looking for soon enough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n\n130\n\n09:32 \n\nBut don’t forget, {{name}}…\n\n131\n\n09:34 \n\nHow you perceive yourself should not be determined by your love life.\n\n132\n\n09:39 \n\nYour spouse or partner is meant to play a supporting role in your life…\n\n133\n\n09:43 \n\nNot the lead.\n\n134\n\n09:44 \n\nFocus on your own personal growth, and I promise you, {{name}}…\n\n135\n\n09:48 \n\nYou will soon discover heavenly rewards."

