module.exports = "00:02 \n\n1\n\nHello {{name}},\n\n00:04 \n\n2\n\nIf you’ve made it here..\n\n00:07 \n\n3\n\nThen congratulations!\n\n00:08 \n\n4\n\nBecause this means that your archetype is the Outlaw!\n\n00:12 \n\n5\n\nWhat does this actually mean?\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/outlaw.png', fadeIn: true} {{/fn}}\n\n00:14 \n\n6\n\nDon’t worry, everything you need to know will be on this page.\n\n00:18 \n\n7\n\nStick to the end of it...\n\n00:19 \n\n8\n\nAnd I promise that you’ll experience something truly magical.\n\n00:25 \n\n9\n\nThe quiz that you’ve just taken is designed to identify your archetype...\n\n00:30 \n\n10\n\nA personality profile that’s broken down your entire being...\n\n{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/outlaw.png', fadeOut: true} {{/fn}}\n\n00:34 \n\n11\n\nYour tendencies, your strengths, your past, and your future.\n\n00:39 \n\n12\n\nYour archetype takes into account your gender, your marital status...\n\n00:43 \n\n13\n\nHow you answered the quiz, and how long you took to get those answers.\n\n00:49 \n\n14\n\nThat’s right, {{name}}.\n\n00:52 \n\n15\n\nYou can use the information to make astronomical changes in your life.\n\n00:56 \n\n16\n\nYou’ll be able to leverage on your strengths, improve on your weaknesses...\n\n01:01 \n\n17\n\nAnd embark on the life that you’ve always dreamed of.\n\n01:05 \n\n18\n\nBut before we get to that...\n\n01:06 \n\n19\n\nI want to share with you about your archetype, The Outlaw, and what it means.\n\n01:14 \n\n20\n\n{{name}}, you truly are a unique individual.\n\n01:19 \n\n21\n\nAnd I genuinely believe that this world needs more people like you.\n\n01:24 \n\n22\n\nYou are a free-minded and free-spirited individual.\n\n01:28 \n\n23\n\nNothing stands in your way and nothing controls your desires.\n\n01:32 \n\n24\n\nYou are the true representation of what it really means to be the captain of your own ship.\n\n01:39 \n\n25\n\nYou are different in that way, {{name}}.\n\n01:42 \n\n26\n\nWhile the world walks in one direction, you have no qualms with going against it.\n\n01:47 \n\n27\n\nThat’s what makes you so special, {{name}}.\n\n01:52 \n\n28\n\nYou’re deeply sensitive when it comes to the social and environmental issues of the world.\n\n01:57 \n\n29\n\nYou’re able to empathize with anyone and everyone.\n\n02:01 \n\n 30\n\nThat’s because as an Outlaw, you are highly compassionate.\n\n02:05 \n\n31\n\nYou put the needs of others before your own...\n\n02:08 \n\n32\n\nAnd you’re able to connect with strangers just as well as...\n\n02:11 \n\n33\n\nyou’re able to connect with your closest loved ones.\n\n02:16 \n\n34\n\n{{name}}, you may not know it yet...\n\n02:20 \n\n35\n\nBut you are more morally and ethically upright than others.\n\n02:23 \n\n36\n\nYou hold your values and principles close to your heart...\n\n02:26 \n\n37\n\nAnd you’re always ready to stick to your guns.\n\n02:30 \n\n38\n\n{{#fn}} {fn: 'sliderImageGlobe', fadeIn: true} {{/fn}}\n\nThere is something else about you, {{name}}…\n\n02:35 \n\n39\n\nYou are the one who will ignite massive change in the world.\n\n02:40 \n\n40\n\n{{name}}, I’m getting the sense that you’re going to live a long, and happy life.\n\n02:48 \n\n41\n\nAlthough success might not be at your feet right now,\n\n02:51 \n\n42\n\nyour riches and fulfilment are merely waiting for you.\n\n02:57 \n\n43\n\n{{#fn}} {fn: 'sliderImageGlobe', fadeOut: true} {{/fn}}\n\n{{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}\n\nBut there’s something else, {{name}}.\n\n03:01 \n\n44\n\nEven though you will triumph, your path to success will not come easy.\n\n03:07 \n\n45\n\nYou must challenge yourself with trusting people.\n\n03:10 \n\n 46\n\nBeing vulnerable can be an incredibly scary thought for you,\n\n03:14 \n\n47\n\nbut it’s important to take small steps towards the direction...\n\n03:19 \n\n48\n\nAnd make a few minor mistakes along the way.\n\n03:22 \n\n49\n\nThis can be applied to both your personal and work relationships.\n\n03:27 \n\n50\n\n{{name}}, don’t let that discourage you.\n\n03:31 \n\n51\n\nIt never hurts to open up once in a while to a loved one, such as family or close friends.\n\n03:38 \n\n52\n\nThe world can be a vicious place if you’re standing alone; even if that thought doesn’t scare you,\n\n03:45 \n\n53\n\nit should definitely be something worth pondering about.\n\n03:48 \n\n54\n\nThe world needs people like you to be in positions of power.\n\n 03:53 \n\n55\n\nYou are the one who keeps the world grounded.\n\n03:56 \n\n56\n\nYou do, however, need to understand that it’s important for you...\n\n04:00 \n\n57\n\nTo combat your cynicism by being more emotionally connected.\n\n04:05 \n\n58\n\nThat way, you’ll be able to build trust and create even more fruitful interactions.\n\n04:12 \n\n59\n\nAfter all, {{name}}, the path to enlightenment and fulfilment lies within your hands.\n\n04:23 \n\n60\n\n{{name}}, I can already tell that your friends and family really look up to you.\n\n04:29 \n\n61\n\nYou’re always ready to stand up for those in need, even if it means that...\n\n04:35 \n\n62\n\nYou will be persecuted for your actions.\n\n04:40 \n\n63\n\nYou’re not the type of person to have huge circles of friends...\n\n04:43 \n\n64\n\nBut you’re not someone who has trouble with making friends.\n\n04:47 \n\n65\n\nIn fact, you have no trouble at all when it comes to...\n\n04:50  \n\n66\n\nimpressing others with your intelligence and passionate nature.\n\n04:54 \n\n67\n\nYou are extremely selective with who you choose to let into your life.\n\n04:59 \n\n68\n\nYou prefer to keep your loved ones close, and your wolf pack small.\n\n05:06 \n\n69\n\n{{name}}, you are incredibly brave and find comfort in chaos.\n\n05:13 \n\n70\n\nYou tend to struggle with being understood...\n\n05:16 \n\n71\n\nBut you know deep down in your heart that your intentions are always centred...\n\n05:20 \n\n72\n\nAround the greater good – even if your strategies and mannerisms speak otherwise.\n\n05:28 \n\n73\n\n{{name}}, you are a natural-born leader.\n\n05:33 \n\n74\n\nI sense that you will find success and fulfilment as an entrepreneur, an activist, or a philanthropist.\n\n05:41 \n\n75\n\nYou can be very authoritative, {{name}}.\n\n05:46 \n\n76\n\nYou tend to want things done your way, on your time...\n\n05:50 \n\n77\n\nEven if your demands are exceedingly difficult to fulfil.\n\n05:54 \n\n78\n\nNevertheless, {{name}}, you are going to make a lasting mark in your lifetime.\n\n06:02 \n\n79\n\nYou’re able to look at the world from a broad and altruistic perspective.\n\n06:06 \n\n80\n\nYou see the big picture, and you have what it takes to make this world a better place.\n\n06:14 \n\n81\n\n{{name}}, I sense that you despise systems and everything that comes with it...\n\n06:20 \n\n82\n\nWhich is why you’re constantly rebelling against authority.\n\n06:23 \n\n83\n\nYou would do anything and everything you can to promote your own ways...\n\n06:29 \n\n84\n\nAnd your own beliefs, which becomes clearly evident in your slightly destructive nature.\n\n06:37 \n\n85\n\nUltimately, {{name}}, you are a visionary.\n\n06:42 \n\n86\n\nYou have a clear idea of how you want to be in the world.\n\n06:45 \n\n87\n\nYou do, however, tend to have difficulty discerning right from wrong.\n\n06:50 \n\n88\n\nI’m getting the sense that you’re not too concerned about monetary riches...\n\n06:55 \n\n89\n\nAlthough you do wish you had more wealth.\n\n06:58 \n\n90\n\nThere is, however, a greater purpose for you in this lifetime, {{name}}.\n\n07:05 \n\n91\n\nHowever, {{name}}, that can make you dependent on others for your own happiness.\n\n07:13 \n\n92\n\nThat is something that you need to be careful of.\n\n07:17 \n\n93\n\nAt times, it might feel as if you’re living your life for others.\n\n07:22 \n\n94\n\nThat’s not what you want, {{name}}.\n\n07:26 \n\n95\n\nYou want to live life for yourself. You want to love yourself.\n\n07:30 \n\n96\n\nWhen you’re able to do that, success and the life of your dreams will follow.\n\n07:35 \n\n97\n\nAnd that’s what Individuation aims to teach you.\n\n07:39 \n\n98\n\nBut there’s more to what I’ve learned about you, {{name}}…\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n\n07:43 \n\n99\n\nIt feels to me that you’re a deeply emotional and spiritual person.\n\n07:49 \n\n100\n\nYou’re connected to your soul in a way that’s far more profound than others.\n\n07:54 \n\n101\n\nYour thought process does not follow the rules and logic of the world.\n\n07:58 \n\n102\n\nIn fact, I get the sense that you have quite the imagination.\n\n08:03 \n\n103\n\nYour thoughts and dreams are vivid, colourful...\n\n08:06 \n\n104\n\nAnd symbolic of who you are as an individual.\n\n{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n\n08:12 \n\n105\n\n{{name}}, you’re not someone who likes to argue.\n\n08:17 \n\n106\n\nYou tend to avoid conflict and you’re uncanny when it comes to devising win-win situations.\n\n08:23 \n\n107\n\nBut when backed into a corner, you’re not one to back down.\n\n08:28 \n\n108\n\nYou fight fiercely for your loved ones and for righteousness.\n\n08:34 \n\n109\n\nYou’re the first person to step in when you see a fight breaking out.\n\n08:39 \n\n110\n\nYou’re the first person to reach for your pockets in the sight of a struggling busker.\n\n08:44 \n\n111\n\nYou’re the first person your friends call when they’ve had a horrible day.\n\n08:49 \n\n112\n\nYou’re the one who people rely on and look up to. \n\n08:53 \n\n113\n\n{{name}}, you are the role model for the world.\n\n08:58 \n\n114\n\nBut every superhero needs a side-kick; a partner in crime.\n\n09:04 \n\n115\n\nOne who will stick by them when the going gets tough. And the outlaw is no exception.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n\n09:10 \n\n116\n\nThis little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n\n09:16 \n\n117\n\nOr if you’re already in a committed relationship...\n\n09:19 \n\n118\n\nThere’s a real good chance that your spouse is one of those archetypes.\n\n09:25 \n\n119\n\n{{name}}, you’re not the kind of person that has trouble when it comes to love.\n\n09:31 \n\n120\n\nPeople find themselves drawn to your charisma and kind nature.\n\n09:36 \n\n121\n\nYou’re sort of a rare breed when it comes to love.\n\n09:39 \n\n122\n\nYou communicate better than anyone else...\n\n09:42 \n\n123\n\nAnd you’re an incredibly thoughtful individual.\n\n09:45 \n\n124\n\nThe relationships that you forge with potential lovers are always profound and deep.\n\n09:53 \n\n125\n\nYou become very selective with those who you choose to date...\n\n09:58 \n\n126\n\nAnd who you choose to end up with – for good reason, of course.\n\n10:03 \n\n127\n\nI sense that in the coming years...\n\n10:05 \n\n128\n\nYou will be the happiest you’ve ever been in that regard.\n\n10:12 \n\n129\n\n{{name}}, I’m sensing that your love life isn’t going to be the smoothest...\n\n10:16 \n\n130\n\nBut you will find that happiness that you’re looking for soon enough.\n\n{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n\n10:23 \n\n131\n\nBut don’t forget, {{name}}, how you perceive yourself should not be determined by your love life.\n\n10:30 \n\n132\n\nYour spouse or partner is meant to play a supporting role in your life; not the lead.\n\n10:38 \n\n133\n\nFocus on your own personal growth...\n\n10:40 \n\n134\n\nAnd I promise you, {{name}}...\n\n10:43 \n\n135\n\nYou will soon discover heavenly rewards."

