module.exports = "1.000:\n1.000: Hello {{name}},\n2.000: If you’ve made it here,\n2.000: then congratulations!\n3.000: Because this means that your archetype is the Ruler!{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/ruler.png', fadeIn: true} {{/fn}}\n3.000: But what does this actually mean?\n5.000: Don’t worry, everything you need to know will be on this page.\n5.000: Stick to the end of it and I promise that you’ll experience something truly magical.{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/ruler.png', fadeOut: true} {{/fn}}\n6.000: The quiz that you’ve just taken is designed to identify your archetype\n4.000: – a personality profile that’s broken down your entire being.\n5.000: Your tendencies, your strengths, your past, and your future.\n5.000: Your archetype takes into account your gender, your marital status,\n2.000: how you answered the quiz,\n3.000: and how long you took to get those answers.\n3.000: That’s right, {{name}}.\n4.000: You can use that information to make astronomical changes in your life.\n5.000: You’ll be able to leverage on your strengths, improve on your weaknesses,\n3.000: and embark on the life that you’ve always dreamed of.\n3.000: But before we get into that,\n4.000: I want to share with you about your archetype, The Ruler, and what it means.\n5.000: {{name}}, you truly are a unique individual.\n5.000: And I genuinely believe that this world needs more people like you.\n5.000: You are a natural-born leader, and people constantly look to you for direction and advice.\n4.000: While others tend to avoid playing the role of a leader,\n4.000: you feel absolutely comfortable with volunteering to take charge.\n3.000: You’re different that way, {{name}}.\n5.000: While the world walks in one direction, you have no qualms with going against it.\n3.000: That’s what makes you so special, {{name}}.\n5.000: You’re always impartial, fair, and just.\n4.000: You’re able to empathize with anyone and everyone.\n5.000: That’s because as a Ruler, you are highly compassionate, and extremely decisive.\n5.000: However, you are also able to make the most difficult decisions,\n3.000: even at the expense of others for the greater good.\n7.000: {{name}}, you might not know it yet, but you are more morally and ethically upright than others.\n3.000: You hold your values and principles close to your heart,\n3.000: and you’re always ready to stick to your guns.\n3.000: There is something else about you, {{name}}…{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeIn: true} {{/fn}}\n3.000: You will be the one to lead enormous change in the world.{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeOut: true} {{/fn}}\n5.000: {{name}}, I’m getting the sense that you’re going to live a long, and happy life.\n4.000: And although success might not be at your feet right now,\n4.000: your riches and fulfilment are merely waiting for you.\n2.000: {{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}.\n6.000: Even though you will triumph, your path to success will not come easy.\n5.000: You will one day have to face the risk of losing all of your control or power.\n5.000: The sheer thought of becoming a follower rather than a leader scares you to death.\n3.000: {{name}}, don’t let that discourage you.\n3.000: The world needs your wisdom and guidance.\n4.000: The world needs people like you to be in positions of power.\n4.000: You are what keeps the world grounded.\n6.000: You do, however, need to understand that it’s important for you not to overburden yourself with the issues of others,\n3.000: and always remember not to spread yourself too thinly.\n6.000: After all, {{name}}, your greatest challenge will be overcoming yourself.\n4.000: But I noticed that you are willing to do everything and anything it takes,\n2.000: and I have complete faith in your abilities.\n7.000: {{name}}, I can already tell that your friends and family really look up to you.\n7.000: You’re the one who they come to for detailed advice, guidance, or solutions to their dilemmas and problems.\n4.000: You’re not the type of person to have huge circles of friends.\n3.000: You’re not someone who has trouble with making friends.\n5.000: In fact, you have no trouble at all when it comes to impressing others with your leadership and knowledge.\n4.000: But you’re extremely selective with who you choose to let into your life.\n5.000: You prefer to keep your loved ones close, and your wolf pack small.\n4.000: {{name}}, you are incredibly nurturing.\n5.000: You possess the unique ability to connect with others and earn their trust.\n4.000: Your thirst for power and control constantly pushes you to new heights,\n5.000: but you must never lose sight of yourself; both as a person, and a leader.\n4.000: You have a great deal to offer to this world. {{name}},\n6.000: you are a responsible and reliable individual, and people see those qualities within you.\n7.000: I sense that you will find success and fulfilment as an entrepreneur, a governor, a lawyer, or a manager.\n3.000: You can be very charismatic, {{name}}.\n7.000: You don’t seem to have any trouble when it comes to establishing the right connections and meeting the right people.\n5.000: {{name}}, you are going to make a lasting mark in your lifetime.\n4.000: You’re able to look at the world from a broad and altruistic perspective.\n6.000: You see the big picture, and you have what it takes to lead this world to a better future.\n6.000: {{name}}, you are an excellent mediator when it comes to managing expectations\n3.000: – those from yourself, and from others.\n5.000: You’re always able to maintain order through your unique sense of understanding what people want.\n5.000: Ultimately, {{name}}, you are a visionary.\n3.000: You dream of an empire that has a throne with your name on it.\n4.000: Discerning right from wrong has never been a difficult task for you.\n4.000: I’m getting the sense that you’re not too concerned about monetary riches,\n3.000: although you do wish you had more wealth.\n4.000: There is, however, a greater purpose for you in this lifetime, {{name}}.\n8.000: However, {{name}}, you must remember that your self-worth is not directly related to your ability to rule.\n5.000: It can feel that way at times, and that is something that you need to be careful of.\n4.000: In fact, it might feel as if you’re living your life for others.\n2.000: That’s not what you want, {{name}}.\n5.000: You want to live life for yourself. You want to love yourself.\n5.000: When you’re able to do that, success and the life of your dreams will follow.\n3.000: And that’s what Individuation aims to teach you.\n4.000: But there’s more to what I’ve learned about you, {{name}}…{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n5.000: It feels to me that you’re a deeply emotional and spiritual person.\n4.000: You’re connected to your soul in a way that’s far more profound than others.\n4.000: Your thought process does not follow the rules and logic of the world.\n5.000: In fact, I get the sense that you have quite the imagination.\n6.000: Your thoughts and dreams are vivid, colourful, and symbolic of who you are as an individual.{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n4.000: {{name}}, you’re not someone who likes to argue.\n6.000: You tend to avoid conflict and you’re uncanny when it comes to devising win-win situations.\n4.000: But when backed into a corner, you’re not one to back down.\n4.000: You fight fiercely for your loved ones and for righteousness.\n4.000: You’re the first person to step in when you see a fight breaking out.\n5.000: You’re the first person to reach for your pockets in the sight of a struggling busker.\n3.000: You’re the first person your friends call when they’ve had a horrible day.\n4.000: You’re the one who people rely on and look up to.\n4.000: {{name}}, you are the leader and protector of the world.\n8.000: But every pilot needs a co-pilot; a partner in crime who will stick by them when the going gets tough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n6.000: This little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n5.000: Or if you’re already in a committed relationship, there’s a really good chance that your spouse is one of those archetypes.\n6.000: {{name}}, you’re not the kind of person that has trouble when it comes to love.\n3.000: People find themselves drawn to your charisma and kind nature.\n3.000: You’re sort of a rare breed when it comes to love.\n5.000: You communicate better than anyone else, and you’re an incredibly thoughtful individual.\n6.000: The relationships that you forge with potential lovers are always profound and deep.\n7.000: You become very selectively with who you choose to date and who you choose to end up with – for good reason, of course.\n5.000: I sense that in the coming years, you will be the happiest you’ve ever been in that regard.\n4.000: {{name}}, I’m sensing that your love life isn’t going to be the smoothest\n3.000: – but you will find that happiness that you’re looking for soon enough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n7.000: But don’t forget, {{name}}, how you perceive yourself should not be determined by your love life.\n5.000: Your spouse or partner is meant to play a supporting role in your life; not the lead.\n3.000: Focus on your own personal growth,\n2.000: and I promise you, {{name}},\n1.000: you will soon discover heavenly rewards."

