module.exports = "2.000:\n2.000: Hello {{name}},\n2.000: If you’ve made it here,\n2.000: then congratulations!\n3.000: Because this means that your archetype is the Member!{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/member.png', fadeIn: true} {{/fn}}\n3.000: But what does this actually mean?\n1.000: Don’t worry {{name}} !\n3.000: Everything you need to know will be on this page.\n1.000: Stick to the end of it\n4.000: and I promise that you’ll experience something truly magical.{{#fn}} {fn: 'sliderImageSingle', path:'images/archetype-icons/member.png', fadeOut: true} {{/fn}}\n5.000: The quiz that you’ve just taken is designed to identify your archetype\n4.000: – a personality profile that’s broken down your entire being.\n5.000: Your tendencies, your strengths, your past, and your future.\n2.000: Your archetype takes into account your gender,\n4.000: your marital status, how you answered the quiz,\n2.000: and how long you took to get those answers.\n2.000: That’s right, {{name}}.\n5.000: You can use that information to make astronomical changes in your life.\n3.000: You’ll be able to leverage on your strengths,\n1.000: improve on your weaknesses,\n4.000: and embark on the life that you’ve always dreamed of.\n4.000: But before we get into that, I want to share with you about your archetype,\n3.000: The Member, and what it means.\n4.000: {{name}}, you are truly a unique individual.\n4.000: And I genuinely believe that this world needs more people like you.\n3.000: You are a mature and deeply intellectual being\n3.000: – not necessarily in the sense of academics,\n6.000: but in terms of connecting with people and building rapport with people from all sorts of backgrounds.\n2.000: You’re different that way, {{name}}..\n3.000: While the world walks in one direction,\n3.000: you have no qualms with going against it.\n3.000: That’s what makes you so special, {{name}}.\n2.000: You’re deeply sensitive.\n4.000: You’re able to empathize with anyone and everyone.\n4.000: That’s because as a Member, you are highly compassionate.\n2.000: You put the needs of others before your own,\n3.000: and you’re able to connect with strangers just as well\n3.000: as you’re able to connect with your closest loved ones.\n3.000: {{name}}, you might not know it yet,\n3.000: but you are more morally and ethically upright than others.\n4.000: You hold your values and principles close to your heart,\n3.000: and you’re always ready to stick to your guns.\n3.000: There is something else about you, {{name}}…{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeIn: true} {{/fn}}\n3.000: You form the foundation of the world.{{#fn}} {fn: 'sliderImageSingle', path:'images/script-images/globe.png', fadeOut: true} {{/fn}}\n4.000: {{name}}, I’m getting the sense that you’re going to live a long, and happy life.\n4.000: And although success might not be at your feet right now,\n4.000: your riches and fulfilment are merely waiting for you.\n2.000: {{#fn}} {fn: 'confirmToContinue', type: 'ok', message: 'Would you like to learn more about your path to fulfilment?', button: 'Click here to continue'} {{/fn}}.\n5.000: Even though you will triumph, your path to success will not come easy.\n3.000: Talking to people is never a problem for you,\n4.000: but I’m getting the sense that you tend to struggle when it comes to talking about yourself.\n4.000: Your nature revolves so much around listening to people\n4.000: that you find it difficult to talk about yourself and share your own experience.\n3.000: {{name}}, don’t let that discourage you.\n4.000: The world needs your selflessness and kindness.\n4.000: The world needs people like you to be in positions of power.\n3.000: You are the one who keeps the world grounded.\n4.000: You do, however, need to understand that it’s important for you to\n3.000: openly and willingly share your story with others.\n4.000: That way, you’ll be able to build trust with the people around you\n3.000: and create even more fruitful interactions.\n5.000: After all, {{name}}, conversation is your forte.\n3.000: And conversation needs more than one person to be involved.\n5.000: {{name}}, I can already tell that your friends and family really look up to you.\n4.000: You’re the one who they come to for detailed advice,\n3.000: a listening ear, or just for a quick chat.\n4.000: You’re not the type of person to have a huge circles of friends.\n3.000: You’re not someone who has trouble with making friends.\n3.000: In fact, you have no trouble at all when it comes to impressing others\n3.000: with your intelligence and sweet nature.\n4.000: But you’re extremely selective with who you choose to let into your life.\n4.000: You prefer to keep your loved ones close, and your wolf pack small.\n3.000: {{name}}, you are incredibly nurturing.\n5.000: You possess the unique ability to connect with others and blend into any social group.\n2.000: You have a great deal to offer to this world.\n5.000: And I’m getting the sense that you are a natural-born leader.\n4.000: You will find success and fulfilment as an entrepreneur, a politician,\n3.000: a public relations manager, or a salesman.\n2.000: You can be very protective, {{name}}.\n3.000: Especially as a parent.\n4.000: You love your children, friends, and family fiercely.\n4.000: You are willing to give and sacrifice everything that you have for them.\n5.000: {{name}}, you are going to make a lasting mark in your lifetime.\n4.000: You’re able to look at the world from a broad and profound perspective.\n6.000: You see the big picture, and you have what it takes to make this world more connected.\n2.000: Each archetype has a calling in life.\n4.000: A calling that will lead you to joy and fulfilment.\n5.000: {{name}}, I’d like to share with you what I’ve discovered about your calling.\n2.000: {{name}}, you are practically a chameleon\n4.000: – possessing the uncanny ability to make yourself at home\n3.000: within any social group or social setting.\n3.000: You excel at one-on-one interactions,\n4.000: and never fail to make an excellent impression on others.\n3.000: Your charisma and eloquence intrigues people.\n4.000: Ultimately, {{name}}, you are a realist.\n2.000: You have a clear conscience.\n4.000: Discerning right from wrong has never been a difficult task for you.\n4.000: I’m getting the sense that you’re not too concerned about monetary riches,\n3.000: although you do wish you had more wealth.\n4.000: There is, however, a greater purpose for you in this lifetime, {{name}}.\n4.000: A purpose that will reveal itself to you very soon.\n5.000: However, {{name}}, you do tend to do things for the sake of others.\n5.000: Your kind and giving nature can make you dependent on others for your own happiness.\n2.000: And that is something that you need to be careful of.\n4.000: At times, it might feel like you’re living your life for others.\n2.000: That’s not what you want, {{name}}.\n3.000: You want to live life for yourself.\n1.000: You want to love yourself.\n5.000: When you’re able to do that, success and the life of your dreams will follow.\n4.000: And that’s what Individuation aims to teach you.\n3.000: But there’s more to what I’ve learned about you, {{name}}…\n4.000: It feels to me that you’re a deeply emotional and spiritual person.{{#fn}} {fn: 'sliderImageSpiritual', fadeIn: true} {{/fn}}\n5.000: You’re connected to your soul in a way that’s far more profound than others.\n4.000: Your thought process does not follow the rules of logic of the world.\n4.000: In fact, I get the sense that you have quite the imagination.\n3.000: Your thoughts and dreams are vivid, colourful,\n3.000: and symbolic of who you are as an individual.{{#fn}} {fn: 'sliderImageSpiritual', fadeOut: true} {{/fn}}\n3.000: {{name}}, you’re not someone who likes to argue.\n3.000: You tend to avoid conflict and you’re uncanny\n3.000: when it comes to devising win-win situations.\n4.000: But when backed into a corner, you’re not one to back down.\n4.000: You fight fiercely for your loved ones and for righteousness.\n4.000: You’re the first person to step in when you see a fight breaking out.\n4.000: You’re the first person to reach out to the quietest person in the room.\n6.000: You’re the first person to extend help and guidance to the weak and the bullied.\n2.000: People look up to you and rely on you.\n4.000: {{name}}, you bring understanding to the misunderstood.\n3.000: But every superhero needs a side-kick;\n4.000: a partner in crime who will stick by them when the going gets tough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeIn: true} {{/fn}}\n6.000: This little chart I’ve prepared represents the archetypes that you’re most likely to end up with.\n3.000: Or if you’re already in a committed relationship,\n4.000: there’s a really good chance that your spouse is one of those archetypes.\n5.000: {{name}}., you’re not the kind of person that has trouble when it comes to love.\n4.000: Love interests find themselves drawn to your charisma and kind nature.\n3.000: You’re sort of a rare breed when it comes to love.\n3.000: You communicate better than anyone else,\n3.000: and you’re an incredibly thoughtful individual.\n5.000: The relationships that you forge with potential lovers are always profound and deep.\n8.000: You become very selective with who you choose to date and who you choose to end up with – for good reason, of course.\n5.000: I sense that in the coming years, you will be the happiest you’ve ever been in that regard.\n4.000: {{name}}, I’m sensing that your love life isn’t going to be the smoothest\n4.000: – but you will find that happiness that you’re looking for soon enough.{{#fn}} {fn: 'sliderImageLoveCompat', fadeOut: true} {{/fn}}\n2.000: But don’t forget, {{name}},\n4.000: how you perceive yourself should not be determined by your love life.\n6.000: Your spouse or partner is meant to play a supporting role in your life; not to lead.\n1.000: Focus on your own personal growth, and I promise you, {{name}}, you will soon discover heavenly rewards."

